/* Main Layout Container */
.flight-itinerary-container {
    display: flex;
    gap: 30px;
    max-width: 1400px;
    margin: 0 auto;
    padding: 20px;
}

/* Loading State */
.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 400px;
    gap: 20px;
}

.loading-spinner-flight {
    width: 10px;
    height: 10px;
    border: 4px solid #f3f3f3;
    border-top: 4px solid #007bff;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.loading-container p {
    color: #666;
    font-size: 16px;
    margin: 0;
}

/* Left Section - Flight Details */
.flight-details-section {
    flex: 2;
    min-width: 0; /* Allows flex item to shrink below content size */
}

/* Right Section - Fare Summary */
.fare-summary-section {
    flex: 1;
    min-width: 300px; /* Minimum width to prevent too much shrinking */
}

/* Responsive Design */
@media (max-width: 1024px) {
    .flight-itinerary-container {
        flex-direction: column;
        gap: 20px;
    }
    
    .fare-summary-section {
        min-width: unset;
        order: -1; /* Show fare summary first on mobile */
    }
}

@media (max-width: 768px) {
    .flight-itinerary-container {
        padding: 10px;
        gap: 15px;
    }
}
/* Main Content */
.main-content {
    display: flex;
    max-width: 1600px;
    margin: 0 auto;
    gap: 40px;
    padding: 0 30px;
}


/* Flight Details Section (Left) */
.flight-details-section {
    flex: 2;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    margin-bottom: 100px;
}


.section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;
    padding-bottom: 25px;
    border-bottom: 2px solid #e2e8f0;
}


.passenger-details-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;
    padding-bottom: 25px;
    border-bottom: 2px solid #e2e8f0;
}


.section-header h2,
.passenger-details-header h2 {
    font-size: 32px;
    font-weight: 800;
    color: #1a202c;
    margin: 0;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}


.back-to-search {
    color: #4299e1;
    text-decoration: none;
    font-weight: 600;
    font-size: 15px;
    padding: 12px 20px;
    border-radius: 12px;
    background: rgba(66, 153, 225, 0.1);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    border: 1px solid rgba(66, 153, 225, 0.2);
}


.back-to-search:hover {
    background: rgba(66, 153, 225, 0.15);
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(66, 153, 225, 0.3);
}


/* Flight Leg */
.flight-leg {
    margin-bottom: 0;
    border: none;
    border-radius: 0;
    overflow: hidden;
}


.leg-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
    padding: 25px 30px;
    border-bottom: 1px solid #e2e8f0;
    margin: 0 30px;
    border-radius: 16px 16px 0 0;
}


.leg-route {
    font-size: 22px;
    font-weight: 700;
    color: #2d3748;
    letter-spacing: 0.3px;
}


.leg-duration {
    background: linear-gradient(135deg, #ff6b6b 0%, #ee5a24 100%);
    color: white;
    padding: 10px 20px;
    border-radius: 25px;
    font-weight: 700;
    font-size: 14px;
    box-shadow: 0 4px 15px rgba(238, 90, 36, 0.3);
    letter-spacing: 0.5px;
}


/* Flight Segment */
.flight-segment {
    padding: 30px;
    border-bottom: 1px solid #f1f5f9;
    transition: all 0.3s ease;
}


.flight-segment:hover {
    background: rgba(247, 250, 252, 0.5);
    transform: translateY(-2px);
}


.flight-segment:last-child {
    border-bottom: none;
}


.segment-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
}


.airline-info {
    display: flex;
    align-items: center;
    gap: 18px;
}


.airline-logo {
    width: 60px;
    height: 60px;
    object-fit: contain;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    border: 2px solid #f1f5f9;
    transition: all 0.3s ease;
}


.airline-logo:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}


.airline-details {
    display: flex;
    flex-direction: column;
}


.airline-name {
    font-weight: 700;
    color: #4c6bd1;
    font-size: 18px;
    margin-bottom: 4px;
}

.airline-code-flight {
    display: flex;
    align-items: center;
    gap: 8px;
}

.airline-code {
    color: #718096;
    font-size: 14px;
    font-weight: 500;
    background-color: #f7fafc;
    padding: 2px 6px;
    border-radius: 4px;
    border: 1px solid #e2e8f0;
}

.flight-number {
    color: #718096;
    font-size: 14px;
    font-weight: 500;
}


.segment-duration {
    background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);
    color: white;
    padding: 8px 16px;
    border-radius: 20px;
    font-weight: 700;
    font-size: 13px;
    box-shadow: 0 4px 15px rgba(72, 187, 120, 0.3);
    letter-spacing: 0.5px;
    white-space: nowrap;
    display: inline-block;
}


/* Segment Details */
.segment-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
}


.departure-info,
.arrival-info {
    text-align: center;
    flex: 1;
    /* padding: 20px;
    background: rgba(247, 250, 252, 0.5);
    border-radius: 16px;
    border: 1px solid #e2e8f0;
    transition: all 0.3s ease; */
}


/* .departure-info:hover,
.arrival-info:hover {
    background: rgba(237, 242, 247, 0.8);
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
} */


/* .time {
    font-size: 24px;
    font-weight: 400;
    color: #1a202c;
    margin-bottom: 8px;
    font-family: 'JetBrains Mono', monospace;
} */


.date {
    font-size: 15px;
    color: #718096;
    margin-bottom: 10px;
    /* font-weight: 500; */
}


.city {
    font-size: 16px;
    font-weight: 700;
    color: #2d3748;
    margin-bottom: 6px;
}


.airport {
    font-size: 14px;
    color: #718096;
    margin-bottom: 4px;
    font-weight: 500;
}

.airport-country {
    font-size: 13px;
    color: #4a5568;
    font-weight: 600;
    font-style: italic;
}


.terminal {
    font-size: 12px;
    color: #a0aec0;
    font-weight: 600;
    background: rgba(160, 174, 192, 0.1);
    padding: 4px 8px;
    border-radius: 8px;
    display: inline-block;
}


.gate {
    font-size: 12px;
    color: #a0aec0;
    margin-top: 4px;
    font-weight: 600;
    background: rgba(160, 174, 192, 0.1);
    padding: 4px 8px;
    border-radius: 8px;
    display: inline-block;
}


/* Flight Connection */
.flight-connection {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
    padding: 20px;
}


.connection-line {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
}


.line {
    height: 3px;
    background: linear-gradient(90deg, #e2e8f0 0%, #cbd5e0 100%);
    flex: 1;
    border-radius: 2px;
}


.arrow {
    color: #4299e1;
    font-size: 20px;
    margin: 0 15px;
    background: white;
    padding: 8px;
    border-radius: 50%;
    box-shadow: 0 4px 15px rgba(66, 153, 225, 0.3);
}


.connection-type {
    font-size: 13px;
    color: #718096;
    font-weight: 600;
    background: rgba(66, 153, 225, 0.1);
    padding: 6px 12px;
    border-radius: 12px;
    border: 1px solid rgba(66, 153, 225, 0.2);
}


/* Segment Features */
.segment-features {
    display: flex;
    gap: 15px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}


.fare-types,
.meal-info,
.refundable,
.fare-label {
    background: linear-gradient(135deg, #ebf8ff 0%, #bee3f8 100%);
    color: #2b6cb0;
    padding: 8px 16px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
    border: 1px solid rgba(59, 130, 246, 0.2);
    transition: all 0.3s ease;
}


.fare-types:hover,
.meal-info:hover,
.refundable:hover,
.fare-label:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 15px rgba(59, 130, 246, 0.2);
}


.fare-label {
    background: linear-gradient(135deg, #fffaf0 0%, #fed7aa 100%);
    color: #c05621;
    border-color: rgba(245, 101, 101, 0.2);
}


/* Trip Segment Headers */
.trip-segment,
.multicity-segment {
    margin-bottom: 35px;
    border: 1px solid #e2e8f0;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.08);
    transition: all 0.3s ease;
}


.trip-segment:hover,
.multicity-segment:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.12);
}


.trip-header,
.multicity-header {
    background: linear-gradient(135deg, #4299e1 0%, #3182ce 100%);
    color: white;
    padding: 20px 25px;
    margin: 0;
    position: relative;
    overflow: hidden;
}


.trip-header::before,
.multicity-header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
    animation: shimmer 3s infinite;
}


@keyframes shimmer {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}


.trip-header h3,
.multicity-header h3 {
    margin: 0;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    position: relative;
    z-index: 1;
}


.segment-date {
    margin-top: 8px;
    font-size: 15px;
    font-weight: 400;
    opacity: 0.9;
    text-align: center;
    position: relative;
    z-index: 1;
}


.multicity-segment {
    background: linear-gradient(135deg, #fafbfc 0%, #f7fafc 100%);
}


.multicity-header {
    background: linear-gradient(135deg, #ed8936 0%, #dd6b20 100%);
}


/* Multi-city Info Section */
.multicity-info {
    background: linear-gradient(135deg, #ebf8ff 0%, #bee3f8 100%);
    border: 1px solid #4299e1;
    border-radius: 16px;
    padding: 20px;
    margin-top: 25px;
    display: flex;
    align-items: flex-start;
    gap: 15px;
    box-shadow: 0 4px 15px rgba(66, 153, 225, 0.1);
}


.info-icon {
    font-size: 22px;
    margin-top: 2px;
    color: #3182ce;
}


.info-text {
    color: #2b6cb0;
    font-size: 15px;
    line-height: 1.5;
    font-weight: 500;
}


.info-text .warning {
    color: #e53e3e;
    font-weight: 600;
    background: rgba(229, 62, 62, 0.1);
    padding: 8px 12px;
    border-radius: 8px;
    margin-top: 8px;
    display: inline-block;
}


/* Baggage Info */
.segment-baggage {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
    padding: 15px;
    background: rgba(247, 250, 252, 0.8);
    border-radius: 12px;
    border: 1px solid #e2e8f0;
}


.baggage-icon {
    font-size: 20px;
    color: #4299e1;
}


.baggage-details {
    font-size: 14px;
    color: #4a5568;
    font-weight: 500;
}


/* Layover Info */
.layover-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 25px 0;
    padding: 20px;
    background: linear-gradient(135deg, #fffaf0 0%, #fed7aa 100%);
    border-radius: 16px;
    border: 1px solid #f6ad55;
    box-shadow: 0 4px 15px rgba(245, 101, 101, 0.1);
}


.layover-line {
    width: 100%;
    height: 3px;
    background: linear-gradient(90deg, #f6ad55 0%, #ed8936 100%);
    margin-bottom: 12px;
    border-radius: 2px;
}


.layover-text {
    font-size: 15px;
    color: #c05621;
    font-weight: 600;
    margin-bottom: 8px;
}


.layover-time {
    background: linear-gradient(135deg, #ed8936 0%, #dd6b20 100%);
    color: white;
    padding: 6px 16px;
    border-radius: 20px;
    font-size: 13px;
    font-weight: 700;
    box-shadow: 0 4px 15px rgba(237, 137, 54, 0.3);
}


/* Action Buttons */
.action-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 35px;
    padding-top: 25px;
    border-top: 2px solid #e2e8f0;
}


.fare-rules-btn {
    background: linear-gradient(135deg, #ffffff 0%, #f7fafc 100%);
    color: #4299e1;
    border: 2px solid #4299e1;
    padding: 15px 28px;
    border-radius: 12px;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    font-size: 15px;
    box-shadow: 0 4px 15px rgba(66, 153, 225, 0.1);
}


.fare-rules-btn:hover {
    background: linear-gradient(135deg, #4299e1 0%, #3182ce 100%);
    color: white;
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(66, 153, 225, 0.3);
}


.navigation-buttons {
    display: flex;
    gap: 18px;
}


.back-btn,
.next-btn {
    padding: 18px 35px;
    border: none;
    border-radius: 12px;
    font-weight: 700;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    letter-spacing: 0.5px;
    position: relative;
    overflow: hidden;
}


.back-btn {
    background: linear-gradient(135deg, #718096 0%, #4a5568 100%);
    color: white;
    box-shadow: 0 4px 15px rgba(113, 128, 150, 0.3);
}


.back-btn:hover {
    background: linear-gradient(135deg, #4a5568 0%, #2d3748 100%);
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(113, 128, 150, 0.4);
}


.next-btn {
    background: linear-gradient(135deg, #4299e1 0%, #3182ce 100%);
    color: white;
    box-shadow: 0 4px 15px rgba(66, 153, 225, 0.3);
}


.next-btn:hover {
    background: linear-gradient(135deg, #3182ce 0%, #2c5aa0 100%);
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(66, 153, 225, 0.4);
}


/* Fare Summary Section (Right) */
.fare-summary-section {
    flex: 1;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    padding: 30px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    height: fit-content;
    position: sticky;
    top: 30px;
    border: 1px solid rgba(255, 255, 255, 0.2);
}


.fare-summary-section h3 {
    font-size: 22px;
    font-weight: 800;
    color: #1a202c;
    margin: 0 0 30px 0;
    text-align: center;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}


/* Segment Fare Breakdown */
.segment-fare-breakdown {
    margin-bottom: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid #e2e8f0;
}


.segment-fare-breakdown h4 {
    margin: 0 0 18px 0;
    color: #2d3748;
    font-size: 17px;
    font-weight: 700;
}


.segment-fare-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;
    font-size: 14px;
    color: #718096;
    transition: all 0.3s ease;
}


.segment-fare-item:hover {
    background: rgba(247, 250, 252, 0.8);
    border-radius: 8px;
    padding-left: 8px;
    padding-right: 8px;
}


.segment-fare-item span:first-child {
    flex: 1;
    margin-right: 15px;
    font-weight: 500;
}


.segment-fare-item span:last-child {
    font-weight: 700;
    color: #2d3748;
}


.segment-fare-separator {
    height: 2px;
    background: linear-gradient(90deg, #e2e8f0 0%, #cbd5e0 100%);
    margin: 18px 0;
    border-radius: 1px;
}


.fare-breakdown {
    display: flex;
    flex-direction: column;
    gap: 25px;
}


.fare-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 18px 0;
    border-bottom: 1px solid #f1f5f9;
    transition: all 0.3s ease;
}


.fare-item:hover {
    background: rgba(247, 250, 252, 0.5);
    border-radius: 8px;
    padding-left: 8px;
    padding-right: 8px;
}


.fare-item:last-child {
    border-bottom: none;
}


.fare-item span:first-child {
    color: #718096;
    font-size: 15px;
    font-weight: 500;
}


.fare-item span:last-child {
    font-weight: 700;
    color: #2d3748;
    font-size: 17px;
}


.fare-item.total {
    font-size: 20px;
    font-weight: 800;
    color: #4299e1;
    border-top: 3px solid #4299e1;
    padding-top: 25px;
    margin-top: 15px;
    background: linear-gradient(135deg, rgba(66, 153, 225, 0.05) 0%, rgba(66, 153, 225, 0.1) 100%);
    border-radius: 12px;
    padding: 20px;
}


.fare-item.expandable {
    cursor: pointer;
    position: relative;
}


.fare-item.expandable::after {
    content: '';
    color: #a0aec0;
    font-size: 12px;
    margin-left: 10px;
    transition: transform 0.3s ease;
}


.fare-item.expandable:hover::after {
    transform: rotate(180deg);
}


/* Session Footer */
.session-footer {
    background: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);
    color: white;
    text-align: center;
    padding: 20px 0;
    margin-top: 60px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
}


.session-footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.05) 50%, transparent 70%);
    animation: shimmer 4s infinite;
}


.session-timer {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    font-size: 15px;
    font-weight: 600;
    position: relative;
    z-index: 1;
}


.timer-icon {
    font-size: 18px;
    color: #f6ad55;
    animation: pulse 2s infinite;
}


@keyframes pulse {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.7; }
}


/* Loading State */
.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-size: 20px;
    color: #718096;
    font-weight: 600;
    background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}


/* Responsive Design */
@media (max-width: 1200px) {
    .main-content {
        max-width: 100%;
        gap: 30px;
        padding: 0 25px;
    }
   
    .passenger-details-container {
        gap: 30px;
        padding: 0 25px;
    }
}


@media (max-width: 1024px) {
    .main-content {
        flex-direction: column;
        gap: 25px;
    }
   
    .fare-summary-section {
        position: static;
        order: -1;
    }
   
    .flight-details-section,
    .fare-summary-section {
        padding: 30px;
    }
   
    .passenger-details-container {
        flex-direction: column;
        gap: 30px;
    }
   
    .fare-summary-section {
        position: static;
        order: -1;
    }
}


@media (max-width: 768px) {
    .progress-steps {
        flex-direction: column;
        gap: 20px;
        padding: 25px 0;
    }
   
    .step {
        margin: 8px 0;
    }
   
    .main-content {
        padding: 0 20px;
        gap: 20px;
    }
   
    .flight-details-section,
    .fare-summary-section {
        padding: 25px;
    }
   
    .section-header {
        flex-direction: column;
        gap: 20px;
        text-align: center;
    }
   
    .section-header h2 {
        font-size: 28px;
    }
   
    .leg-header {
        flex-direction: column;
        gap: 20px;
        text-align: center;
        padding: 20px;
    }
   
    .segment-details {
        flex-direction: column;
        gap: 25px;
    }
   
    .action-buttons {
        flex-direction: column;
        gap: 20px;
    }
   
    .navigation-buttons {
        width: 100%;
        flex-direction: column;
