.e-ticket-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  background: white;
  font-family: Arial, sans-serif;
  color: #000;
  line-height: 1.5;
}

.ticket-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  color: #495057;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #e9ecef;
  border-top-color: #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Header */
.eticket-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #000;
}

.eticket-logo-left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.company-logo-img {
  max-width: 200px;
  max-height: 80px;
  object-fit: contain;
  height: auto;
}

.eticket-contact-right {
  text-align: right;
  font-size: 0.875rem;
}

.company-name {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #000;
}

.contact-info {
  color: #333;
  line-height: 1.6;
}

.contact-info div {
  margin-bottom: 0.25rem;
}

/* Airline Info */
.eticket-airline-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ddd;
}

.airline-logos-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.airline-logo-img {
  width: 60px;
  height: 60px;
  object-fit: contain;
}

.airline-pnr-info {
  display: flex;
  flex-direction: column;
}

.airline-pnr-code {
  font-size: 1.25rem;
  font-weight: bold;
  color: #000;
}

.airline-pnr-label {
  font-size: 0.875rem;
  color: #666;
}

/* Booking Status */
.eticket-booking-status {
  display: flex;
  gap: 2rem;
  margin-bottom: 1.5rem;
  padding: 0.75rem;
  background: #f5f5f5;
  border: 1px solid #ddd;
  font-size: 0.875rem;
}

.booking-time-info,
.booking-id,
.booking-status {
  color: #000;
}

.booking-status strong {
  color: #000;
  font-weight: bold;
}

/* Sections */
.eticket-section {
  margin-bottom: 2rem;
}

.eticket-section-title {
  font-size: 1.125rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #000;
  border-bottom: 2px solid #000;
  padding-bottom: 0.5rem;
}

.flight-note {
  font-size: 0.75rem;
  color: #666;
  margin-bottom: 0.75rem;
  font-style: italic;
}

/* Tables */
.eticket-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
  font-size: 0.875rem;
}

.eticket-table thead {
  background: #f5f5f5;
}

.eticket-table th {
  padding: 0.75rem 0.5rem;
  text-align: left;
  border: 1px solid #000;
  font-weight: bold;
  font-size: 0.875rem;
  color: #000;
}

.eticket-table td {
  padding: 0.75rem 0.5rem;
  border: 1px solid #000;
  vertical-align: top;
  color: #000;
}

.eticket-table tbody tr {
  background: white;
}

.eticket-table tbody tr:nth-child(even) {
  background: #fafafa;
}

/* Flight Table */
.flight-airline-with-logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.flight-airline-logo {
  width: 40px;
  height: 40px;
  object-fit: contain;
  flex-shrink: 0;
}

.flight-airline-info {
  display: flex;
  flex-direction: column;
}

.flight-airline {
  font-weight: bold;
  color: #000;
}

.flight-airline-name {
  font-size: 0.75rem;
  color: #666;
  margin-top: 0.25rem;
}

/* Passenger Table */
.passenger-table td {
  font-size: 0.875rem;
}

/* Barcode */
.barcode-section {
  margin-top: 1rem;
  padding: 1rem;
  text-align: center;
  border: 1px solid #ddd;
  background: #f9f9f9;
}

.barcode-placeholder {
  font-family: monospace;
  font-size: 1.5rem;
  letter-spacing: 0.25rem;
  color: #333;
}

/* Fare Details */
.fare-details-list {
  border: 1px solid #000;
  padding: 1rem;
  background: white;
}

.fare-item {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
  border-bottom: 1px solid #ddd;
  font-size: 0.875rem;
}

.fare-item:last-child {
  border-bottom: none;
}

.fare-item.fare-total {
  border-top: 2px solid #000;
  margin-top: 0.5rem;
  padding-top: 0.75rem;
  font-size: 1rem;
}

/* .fare-label {
  color: #000;
} */

.fare-value {
  color: #000;
  font-weight: 500;
}

.fare-total .fare-value {
  font-weight: bold;
}

.gst-note {
  font-size: 0.75rem;
  color: #666;
  font-weight: normal;
}

/* Important Information */
.important-info-list {
  padding-left: 1.5rem;
  margin: 0;
  font-size: 0.875rem;
  line-height: 1.8;
}

.important-info-list li {
  margin-bottom: 0.75rem;
  color: #000;
}

/* Baggage Restrictions */
.baggage-restrictions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  border: 1px solid #000;
  padding: 1rem;
}

.baggage-dangerous,
.baggage-allowed {
  padding: 1rem;
  border: 1px solid #ddd;
}

.baggage-header-dangerous,
.baggage-header-allowed {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 0.875rem;
}

.danger-icon {
  color: #dc3545;
  font-size: 1.25rem;
  font-weight: bold;
}

.allowed-icon {
  color: #28a745;
  font-size: 1.25rem;
  font-weight: bold;
}

.dangerous-items-grid,
.allowed-items-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.75rem;
}

.dangerous-item,
.allowed-item {
  padding: 0.5rem;
  font-size: 0.75rem;
  border: 1px solid #ddd;
  text-align: center;
  background: white;
}

.dangerous-item {
  border-color: #dc3545;
}

.allowed-item {
  border-color: #28a745;
}

/* Actions */
.eticket-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 2px solid #ddd;
}

