/* Print Styles */
@media print {
  .e-ticket-container {
    padding: 0;
    background: white;
  }

  .eticket-actions {
    display: none;
  }

  .eticket-section {
    break-inside: avoid;
    page-break-inside: avoid;
  }

  .eticket-table {
    break-inside: avoid;
    page-break-inside: avoid;
  }

  .btn {
    display: none;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .e-ticket-container {
    padding: 1rem;
  }

  .eticket-header {
    flex-direction: column;
    gap: 1rem;
  }

  .eticket-contact-right {
    text-align: left;
  }

  .baggage-restrictions {
    grid-template-columns: 1fr;
  }

  .eticket-table {
    font-size: 0.75rem;
  }

  .eticket-table th,
  .eticket-table td {
    padding: 0.5rem 0.25rem;
  }

  .eticket-actions {
    flex-direction: column;
  }

  .btn {
    width: 100%;
  }
}
