/* Business Payment Component Styles */

.business-payment-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f8f9fa;
  min-height: 100vh;
}

.business-payment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 16px;
  color: white;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.business-payment-title {
  font-size: 2.2rem;
  font-weight: 700;
  margin: 0;
}

.business-timer {
  display: flex;
  align-items: center;
  gap: 12px;
  background: rgba(255, 255, 255, 0.2);
  padding: 12px 20px;
  border-radius: 25px;
  backdrop-filter: blur(10px);
}

.timer-icon {
  font-size: 1.5rem;
}

.timer-text {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timer-label {
  font-size: 0.9rem;
  opacity: 0.9;
}

.timer-value {
  font-size: 1.8rem;
  font-weight: 700;
  font-family: 'Courier New', monospace;
}

.timer-critical {
  color: #ff6b6b;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

.business-payment-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* Purpose Display Section */
.purpose-display-section {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.purpose-display-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 16px;
}

.purpose-display-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border-radius: 12px;
  border-left: 4px solid #667eea;
}

.purpose-icon {
  font-size: 2rem;
}

.purpose-text {
  font-size: 1.2rem;
  font-weight: 600;
  color: #2c3e50;
  text-transform: capitalize;
}

/* Business Summary Section */
.business-summary-section {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.business-summary-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 16px;
}

.business-summary-card {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 20px;
  background: linear-gradient(135deg, #e3f2fd 0%, #bbdefb 100%);
  border-radius: 12px;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
}

.summary-label {
  font-weight: 600;
  color: #2c3e50;
}

.summary-value {
  font-weight: 700;
  color: #1976d2;
  font-size: 1.1rem;
}

/* Form Sections - Simplified */

/* Terms Section */
.business-terms-section {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.business-terms-checkbox {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 16px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 2px solid #e9ecef;
}

.business-terms-checkbox input[type="checkbox"] {
  margin-top: 4px;
  width: 18px;
  height: 18px;
  accent-color: #667eea;
}

.business-terms-checkbox label {
  font-size: 0.95rem;
  color: #2c3e50;
  line-height: 1.5;
  cursor: pointer;
}

/* Footer */
.business-payment-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
  padding: 20px 0;
}

.business-payment-footer .btn {
  padding: 14px 28px;
  font-size: 1.1rem;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.2s ease;
  border: none;
  cursor: pointer;
}

.business-payment-footer .btn-secondary {
  background: #6c757d;
  color: white;
}

.business-payment-footer .btn-secondary:hover {
  background: #5a6268;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(108, 117, 125, 0.3);
}

.business-payment-footer .btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.business-payment-footer .btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.business-payment-footer .btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.business-payment-footer .btn.processing {
  background: linear-gradient(135deg, #95a5a6 0%, #7f8c8d 100%);
  cursor: not-allowed;
}

/* Timeout Notification */
.timeout-notification {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #ff6b6b;
  color: white;
  padding: 24px 32px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(255, 107, 107, 0.3);
  display: flex;
  align-items: center;
  gap: 16px;
  z-index: 1000;
  animation: slideInBounce 0.5s ease;
}

.timeout-icon {
  font-size: 2rem;
}

.timeout-message {
  font-size: 1.1rem;
  font-weight: 600;
}

@keyframes slideInBounce {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.3);
  }
  50% {
    transform: translate(-50%, -50%) scale(1.05);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .business-payment-container {
    padding: 10px;
  }

  .business-payment-header {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }

  .business-payment-title {
    font-size: 1.8rem;
  }

  .business-timer {
    padding: 10px 16px;
  }

  .timer-value {
    font-size: 1.5rem;
  }


  .business-payment-footer {
    flex-direction: column;
    gap: 12px;
  }

  .business-payment-footer .btn {
    width: 100%;
    padding: 16px 24px;
  }

  .purpose-display-card {
    flex-direction: column;
    text-align: center;
  }

  .summary-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }

  .timeout-notification {
    margin: 20px;
    padding: 20px;
    flex-direction: column;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .business-payment-title {
    font-size: 1.5rem;
  }

  .business-form-section,
  .approval-section,
  .additional-info-section,
  .business-terms-section {
    padding: 16px;
  }

  .form-input,
  .form-textarea {
    padding: 10px 12px;
  }
}
