/* Login Page Styles */
.login-container {
  display: flex;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* Login Popup Overlay Styles */
.login-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  backdrop-filter: blur(8px);
  animation: fadeIn 0.4s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.login-popup-container {
  display: flex;
  width: 95%;
  max-width: 1100px;
  max-height: 85vh;
  background: white;
  border-radius: 24px;
  overflow: hidden;
  box-shadow: 0 32px 64px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(255, 255, 255, 0.1);
  animation: popupSlideIn 0.5s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes popupSlideIn {
  from {
    opacity: 0;
    transform: translateY(-40px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Left Side - Image Slideshow */
.login-image-section {
  flex: 1.2;
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  background-size: 200% 200%;
  animation: gradientShift 20s ease infinite;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.image-slideshow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transition: all 3s ease-in-out;
  transform: scale(1.05);
  filter: brightness(0.9) contrast(1.1);
}

.slide-image.active {
  opacity: 1;
  transform: scale(1);
  filter: brightness(1) contrast(1);
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(0,0,0,0.4) 0%, rgba(0,0,0,0.2) 50%, rgba(0,0,0,0.1) 100%);
  z-index: 1;
}

.brand-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  text-align: center;
  color: white;
  padding: 3rem 2rem;
  max-width: 480px;
  width: 100%;
}

.brand-logo {
  position: absolute;
  top: 2.5rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}

.brand-logo img {
  width: 200px;
  height: 60px;
  object-fit: contain;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
}

.brand-subtitle {
  font-size: 1.3rem;
  font-weight: 400;
  opacity: 0.95;
  margin-bottom: 2.5rem;
  line-height: 1.7;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.brand-features {
  list-style: none;
  padding: 0;
  margin: 0;
}

.brand-features li {
  padding: 0.75rem 0;
  font-size: 1.1rem;
  opacity: 0.9;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  font-weight: 300;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.brand-features li::before {
  /* content: ""; */
  font-size: 1.2rem;
}

/* Right Side - Login Form */
.login-form-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 2rem;
  background: linear-gradient(135deg, #f8fafc 0%, #ffffff 100%);
  position: relative;
}

.login-form-section .brand-logo {
  position: relative;
  top: auto;
  left: auto;
  transform: none;
  margin-bottom: 2rem;
}

.login-form-section .brand-logo img {
  width: 160px;
  height: 50px;
  filter: none;
}

.login-form-container {
  width: 100%;
  max-width: 420px;
  background: white;
  padding: 2.5rem;
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
}

.login-form-header {
  text-align: center;
  margin-bottom: 2.5rem;
}

.login-form-title {
  font-size: 2.2rem;
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 0.75rem;
  letter-spacing: -0.02em;
}

.login-form-subtitle {
  color: #64748b;
  font-size: 1rem;
  margin-bottom: 0;
  font-weight: 400;
  line-height: 1.5;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.form-label {
  font-weight: 500;
  color: #374151;
  font-size: 0.9rem;
  text-transform: none;
  letter-spacing: 0;
}

.form-input {
  padding: 1rem 1.25rem;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #ffffff;
  color: #1f2937;
  font-weight: 400;
}

.form-input:focus {
  outline: none;
  border-color: #667eea;
  background: #ffffff;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-1px);
}

.form-input::placeholder {
  color: #9ca3af;
  font-weight: 400;
}

.error-msg {
  color: #dc2626;
  font-size: 0.875rem;
  text-align: center;
  padding: 1rem 1.25rem;
  background: #fef2f2;
  border-radius: 10px;
  border: 1px solid #fecaca;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.success-message {
  color: #059669;
  font-size: 0.875rem;
  text-align: center;
  padding: 1rem 1.25rem;
  background: #f0fdf4;
  border-radius: 10px;
  border: 1px solid #bbf7d0;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.login-submit-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 0.5rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.login-submit-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

.login-submit-btn:hover::before {
  left: 100%;
}

.login-submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.4);
}

.login-submit-btn:active {
  transform: translateY(0);
}

/* OTP Input Styles */
.otp-input {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 0.5rem;
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace;
  padding: 1.25rem 1rem;
}

.otp-input:focus {
  letter-spacing: 0.5rem;
}

/* Back Button Styles */
.back-btn {
  background: transparent;
  color: #667eea;
  border: 2px solid #e5e7eb;
  padding: 0.875rem 1.5rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
  width: 100%;
}

.back-btn:hover {
  background: #f8fafc;
  border-color: #667eea;
  color: #667eea;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.15);
}

.back-btn:active {
  transform: translateY(0);
}

/* Privacy Policy Styles */
.privacy-policy {
  margin-top: 2rem;
  text-align: center;
  padding: 0;
}

.privacy-policy p {
  color: #9ca3af;
  font-size: 0.8rem;
  margin: 0;
  font-weight: 400;
  line-height: 1.4;
}

