/* Responsive Design */
@media (max-width: 768px) {
  .login-container {
    flex-direction: column;
  }
  
  .login-popup-container {
    flex-direction: column;
    width: 95%;
    max-height: 95vh;
    border-radius: 20px;
  }
  
  .login-image-section {
    height: 35vh;
    min-height: 280px;
    flex: none;
  }
  
  .brand-content {
    padding: 2rem 1.5rem;
  }
  
  .brand-subtitle {
    font-size: 1.1rem;
    margin-bottom: 2rem;
  }
  
  .brand-features li {
    font-size: 1rem;
    padding: 0.5rem 0;
  }
  
  .login-form-section {
    padding: 2rem 1.5rem;
    flex: none;
  }
  
  .login-form-section .brand-logo {
    margin-bottom: 1.5rem;
  }
  
  .login-form-section .brand-logo img {
    width: 140px;
    height: 45px;
  }
  
  .login-form-container {
    padding: 2rem;
    max-width: 100%;
  }
  
  .login-form-title {
    font-size: 1.8rem;
  }
  
  .login-form-subtitle {
    font-size: 0.9rem;
  }
  
  .otp-input {
    font-size: 1.3rem;
    letter-spacing: 0.3rem;
    padding: 1rem 0.75rem;
  }
}

@media (max-width: 480px) {
  .login-popup-container {
    width: 98%;
    max-height: 98vh;
    border-radius: 16px;
  }
  
  .login-form-container {
    padding: 1.5rem;
  }
  
  .brand-content {
    padding: 1.5rem 1rem;
  }
  
  .brand-subtitle {
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .brand-features li {
    font-size: 0.9rem;
    padding: 0.4rem 0;
  }
  
  .login-form-section .brand-logo img {
    width: 120px;
    height: 35px;
  }
  
  .login-form-title {
    font-size: 1.6rem;
  }
  
  .otp-input {
    font-size: 1.2rem;
    letter-spacing: 0.25rem;
    padding: 0.875rem 0.5rem;
  }
  
  .privacy-policy p {
    font-size: 0.75rem;
  }
}

/* Animation for form elements */
.form-group {
  animation: slideInUp 0.6s ease-out;
  animation-fill-mode: both;
}

.form-group:nth-child(1) { animation-delay: 0.1s; }
.form-group:nth-child(2) { animation-delay: 0.2s; }
.form-group:nth-child(3) { animation-delay: 0.3s; }

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-submit-btn {
  animation: slideInUp 0.6s ease-out 0.4s both;
}

.back-btn {
  animation: slideInUp 0.6s ease-out 0.5s both;
}

.privacy-policy {
  animation: slideInUp 0.6s ease-out 0.6s both;
}

