/* OTP Input Container */
.otp-input-container {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin: 16px 0;
}

.otp-digit-input {
  width: 50px;
  height: 50px;
  border: 2px solid #e1e5e9;
  border-radius: 12px;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  color: #2d3748;
  background: #ffffff;
  transition: all 0.2s ease;
  outline: none;
}

.otp-digit-input:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-2px);
}

.otp-digit-input:not(:placeholder-shown) {
  border-color: #667eea;
  background: #f8f9ff;
}

/* Success Container */
.success-container {
  text-align: center;
  padding: 40px 20px;
}

.success-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #4ade80, #22c55e);
  color: white;
  font-size: 40px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 24px;
  animation: successPulse 0.6s ease-out;
}

@keyframes successPulse {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.success-container .success-message {
  font-size: 18px;
  color: #059669;
  font-weight: 500;
  margin: 0;
}