.json-display-container {
  margin: 20px 0;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background: #f9f9f9;
  overflow: hidden;
}

.json-header {
  background: #2c3e50;
  color: white;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
}

.json-header h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.json-controls {
  display: flex;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
}

.tab-buttons {
  display: flex;
  gap: 5px;
}

.tab-btn {
  padding: 6px 12px;
  border: 1px solid #34495e;
  background: transparent;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s ease;
}

.tab-btn:hover {
  background: #34495e;
}

.tab-btn.active {
  background: #3498db;
  border-color: #3498db;
}

.action-buttons {
  display: flex;
  gap: 8px;
  align-items: center;
}

.search-input {
  padding: 6px 10px;
  border: 1px solid #34495e;
  background: #2c3e50;
  color: white;
  border-radius: 4px;
  font-size: 12px;
  width: 150px;
  transition: all 0.2s ease;
}

.search-input::placeholder {
  color: #bdc3c7;
}

.search-input:focus {
  outline: none;
  border-color: #3498db;
  background: #34495e;
}

.copy-btn, .toggle-btn {
  padding: 6px 12px;
  border: 1px solid #34495e;
  background: transparent;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: all 0.2s ease;
}

.copy-btn:hover, .toggle-btn:hover {
  background: #34495e;
}

.copy-btn:active {
  background: #27ae60;
  border-color: #27ae60;
}

.loading-indicator {
  color: #f39c12;
  font-style: italic;
}

.json-content-container {
  padding: 20px;
  background: white;
}

.json-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 6px;
  border: 1px solid #e9ecef;
}

.stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
}

.stat-label {
  font-weight: 500;
  color: #495057;
}

.stat-value {
  font-weight: 600;
  color: #2c3e50;
  background: #e3f2fd;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 14px;
}

.json-display {
  margin-bottom: 20px;
}

.json-pre {
  background: #2d3748;
  color: #e2e8f0;
  padding: 20px;
  border-radius: 6px;
  overflow-x: auto;
  font-family: 'Courier New', monospace;
  font-size: 13px;
  line-height: 1.5;
  max-height: 500px;
  overflow-y: auto;
  border: 1px solid #4a5568;
}

.json-structured {
  border-top: 2px solid #e9ecef;
  padding-top: 20px;
}

.json-structured h4 {
  margin: 0 0 15px 0;
  color: #2c3e50;
  font-size: 16px;
}

.json-object, .json-array {
  font-family: 'Courier New', monospace;
  font-size: 13px;
  line-height: 1.4;
}

.json-bracket {
  color: #e74c3c;
  font-weight: bold;
}

.json-key {
  color: #3498db;
  font-weight: bold;
}

.json-value {
  color: #27ae60;
}

.json-comma {
  color: #95a5a6;
}

.json-item {
  margin: 2px 0;
}

.json-content {
  border-left: 2px solid #ecf0f1;
  padding-left: 10px;
  margin-left: 10px;
}

/* Responsive design */
@media (max-width: 768px) {
  .json-header {
    flex-direction: column;
    align-items: stretch;
  }
  
  .json-controls {
    justify-content: space-between;
  }
  
  .tab-buttons {
    flex-wrap: wrap;
  }
  
  .json-stats {
    grid-template-columns: 1fr;
  }
  
  .json-pre {
    font-size: 11px;
    padding: 15px;
  }
}

/* Scrollbar styling */
.json-pre::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.json-pre::-webkit-scrollbar-track {
  background: #1a202c;
}

.json-pre::-webkit-scrollbar-thumb {
  background: #4a5568;
  border-radius: 4px;
}

.json-pre::-webkit-scrollbar-thumb:hover {
  background: #718096;
}
