.hbc-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fb 0%, #e8edf5 100%);
  padding: 40px 20px;
}

.hbc-container {
  max-width: 900px;
  margin: 0 auto;
}

.hbc-ticket {
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  padding: 40px;
  margin-bottom: 30px;
  position: relative;
}

/* Ticket Header */
.hbc-header {
  display: flex;
  align-items: center;
  gap: 24px;
  padding-bottom: 24px;
  border-bottom: 2px solid #e5e7eb;
  margin-bottom: 24px;
}

.hbc-logo-section {
  flex-shrink: 0;
}

.hbc-logo {
  width: 80px;
  height: 80px;
  object-fit: contain;
}

.hbc-logo-fallback {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 12px;
}

.hbc-logo-text {
  color: white;
  font-weight: 700;
  font-size: 18px;
}

.hbc-header-info {
  flex: 1;
}

.hbc-title {
  font-size: 28px;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 8px 0;
}

.hbc-booking-id {
  font-size: 16px;
  color: #6b7280;
  font-weight: 600;
  letter-spacing: 0.5px;
}

/* Status Section */
.hbc-status-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background: linear-gradient(135deg, #d1fae5 0%, #a7f3d0 100%);
  border-radius: 12px;
  margin-bottom: 32px;
}

.hbc-status-badge {
  font-size: 16px;
  font-weight: 700;
  padding: 8px 16px;
  border-radius: 8px;
  display: inline-block;
}

.hbc-status-success {
  background: #10b981;
  color: white;
}

.hbc-booking-date {
  font-size: 14px;
  color: #059669;
  font-weight: 500;
}

/* Sections */
.hbc-section {
  margin-bottom: 32px;
  padding-bottom: 24px;
  border-bottom: 1px solid #e5e7eb;
}

.hbc-section:last-of-type {
  border-bottom: none;
}

.hbc-section-title {
  font-size: 20px;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 20px 0;
  padding-bottom: 12px;
  border-bottom: 2px solid #3b82f6;
  display: inline-block;
  width: 100%;
}

/* Hotel Information */
.hbc-hotel-info {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
}

.hbc-hotel-main {
  flex: 1;
  min-width: 250px;
}

.hbc-hotel-name {
  font-size: 24px;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 16px 0;
}

.hbc-hotel-image {
  width: 100%;
  max-width: 300px;
  height: 200px;
  object-fit: cover;
  border-radius: 12px;
  margin-top: 12px;
}

.hbc-hotel-details {
  flex: 1;
  min-width: 250px;
}

.hbc-detail-row {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  border-bottom: 1px solid #f3f4f6;
}

.hbc-detail-row:last-child {
  border-bottom: none;
}

.hbc-detail-label {
  font-weight: 600;
  color: #6b7280;
  font-size: 14px;
}

.hbc-detail-value {
  font-weight: 500;
  color: #1f2937;
  font-size: 14px;
  text-align: right;
}

/* Guest Information - Grid Layout */
.hbc-guests-grid-container {
  background: #e0f2fe;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid #bae6fd;
  margin-bottom: 24px;
}

.hbc-guests-grid-header {
  display: grid;
  grid-template-columns: 100px 150px 1fr auto;
  gap: 16px;
  padding: 16px 20px;
  background: linear-gradient(135deg, #7dd3fc 0%, #38bdf8 100%);
  color: white;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.hbc-guests-grid-header.has-pan {
  grid-template-columns: 100px 150px 1fr 120px;
}

.hbc-grid-header-cell {
  display: flex;
  align-items: center;
}

.hbc-guests-grid-row {
  display: grid;
  grid-template-columns: 100px 150px 1fr auto;
  gap: 16px;
  padding: 16px 20px;
  border-bottom: 1px solid #bae6fd;
  align-items: center;
  transition: background-color 0.2s ease;
  background-color: #f0f9ff;
}

.hbc-guests-grid-row:last-child {
  border-bottom: none;
}

.hbc-guests-grid-row:hover {
  background-color: #bae6fd;
}

.hbc-guests-grid-row.has-pan {
  grid-template-columns: 100px 150px 1fr 120px;
}

.hbc-grid-cell {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #1f2937;
}

.hbc-grid-value {
  font-weight: 500;
  color: #1f2937;
}

.hbc-guest-role-badge {
  font-size: 12px;
  font-weight: 600;
  color: #0369a1;
  background: #bae6fd;
  padding: 6px 12px;
  border-radius: 6px;
  display: inline-block;
  white-space: nowrap;
}

.hbc-grid-label-mobile {
  display: none;
  font-size: 12px;
  color: #6b7280;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-right: 8px;
}

.hbc-contact-info {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 2px solid #e5e7eb;
  display: flex;
  gap: 32px;
  flex-wrap: wrap;
}

.hbc-contact-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.hbc-contact-label {
  font-size: 12px;
  color: #6b7280;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.hbc-contact-value {
  font-size: 15px;
  color: #1f2937;
  font-weight: 500;
}

/* Payment Information */
.hbc-payment-info {
  background: #f9fafb;
  padding: 20px;
  border-radius: 12px;
}

.hbc-payment-row {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #e5e7eb;
}

.hbc-payment-row:last-of-type {
  border-bottom: none;
  margin-bottom: 16px;
}

.hbc-payment-label {
  font-weight: 600;
  color: #6b7280;
  font-size: 14px;
}

.hbc-payment-value {
  font-weight: 500;
  color: #1f2937;
  font-size: 14px;
}

.hbc-payment-breakdown {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 2px solid #e5e7eb;
}

.hbc-payment-item {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  font-size: 15px;
}

.hbc-payment-item:not(.hbc-payment-total) {
  border-bottom: 1px solid #e5e7eb;
}

.hbc-payment-total {
  font-size: 18px;
  font-weight: 700;
  color: #1f2937;
  margin-top: 8px;
  padding-top: 16px;
  border-top: 2px solid #3b82f6;
}

/* Special Request */
.hbc-special-request {
  background: #fef3c7;
  padding: 16px;
  border-radius: 8px;
  border-left: 4px solid #f59e0b;
  color: #92400e;
  font-size: 14px;
  line-height: 1.6;
}

/* Important Information */
.hbc-important-info {
  background: #eff6ff;
  padding: 20px;
  border-radius: 12px;
  border-left: 4px solid #3b82f6;
}

.hbc-info-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.hbc-info-list li {
  padding: 8px 0;
  padding-left: 24px;
  position: relative;
  color: #1e40af;
  font-size: 14px;
  line-height: 1.6;
}

.hbc-info-list li:before {
  content: "";
  position: absolute;
  left: 0;
  color: #3b82f6;
  font-weight: bold;
  font-size: 18px;
}

/* Footer */
.hbc-footer {
  margin-top: 32px;
  padding-top: 24px;
  border-top: 2px solid #e5e7eb;
  text-align: center;
}

.hbc-footer-text {
  font-size: 16px;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 12px;
}

.hbc-footer-contact {
  font-size: 13px;
  color: #6b7280;
}

/* Action Buttons */
.hbc-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 30px;
}

.hbc-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 14px 28px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

.hbc-btn-primary {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: white;
}

.hbc-btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}

.hbc-btn-secondary {
  background: white;
  color: #3b82f6;
  border: 2px solid #3b82f6;
}

.hbc-btn-secondary:hover {
  background: #eff6ff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.2);
}

/* Print Styles */
@media print {
  .hbc-page {
    background: white;
    padding: 0;
  }

  .hbc-actions {
    display: none;
  }

  .hbc-ticket {
    box-shadow: none;
    border: 1px solid #e5e7eb;
  }

  .hbc-section {
    page-break-inside: avoid;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .hbc-ticket {
    padding: 24px;
  }

  .hbc-header {
    flex-direction: column;
    text-align: center;
  }

  .hbc-title {
    font-size: 24px;
  }

  .hbc-hotel-info {
    flex-direction: column;
  }

  .hbc-actions {
    flex-direction: column;
  }

  .hbc-btn {
    width: 100%;
    justify-content: center;
  }

  /* Mobile Grid Layout */
  .hbc-guests-grid-header {
    display: none;
  }

  .hbc-guests-grid-row {
    display: grid;
    grid-template-columns: 1fr;
    gap: 12px;
    padding: 16px;
    border-bottom: 2px solid #e5e7eb;
  }

  .hbc-guests-grid-row.has-pan {
    grid-template-columns: 1fr;
  }

  .hbc-grid-cell {
    display: flex;
    align-items: center;
    padding: 8px 0;
  }

  .hbc-grid-label-mobile {
    display: inline-block;
    min-width: 100px;
  }

  .hbc-guest-role-badge {
    display: inline-block;
  }
}

