/* Tab Switcher Styles */
.tab-container {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-top: 2rem;
}

.tab-header {
  display: flex;
  background: #f8f9fa;
  border-bottom: 2px solid #e9ecef;
  /* overflow-x: auto;
  scrollbar-width: thin; */
}

.tab-header::-webkit-scrollbar {
  height: 4px;
}

.tab-header::-webkit-scrollbar-thumb {
  background: #667eea;
  border-radius: 4px;
}

.tab-button {
  flex: 1;
  min-width: 150px;
  padding: 1rem 1.5rem;
  background: transparent;
  border: none;
  color: #6c757d;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  position: relative;
}

.tab-button::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 2px;
  background: transparent;
  transition: all 0.3s ease;
}

.tab-button:hover {
  background: rgba(102, 126, 234, 0.05);
  color: #667eea;
}

.tab-button.active {
  color: #667eea;
  background: white;
}

.tab-button.active::after {
  background: #667eea;
}

.tab-content {
  padding: 2rem;
}

.tab-panel {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Facilities Panel Styles */
.facilities-panel .section-title {
  margin-bottom: 1.5rem;
}

.facilities-grid {
  display: grid;
