  }

  .table-header > div:last-child,
  .table-row > div:last-child {
    border-bottom: none;
  }

  .table-header {
    display: none;
  }

  .table-row > div::before {
    content: attr(data-label);
    font-weight: 600;
    color: #495057;
    margin-bottom: 0.5rem;
    display: block;
  }

  .col-room-type::before { content: "Room Type"; }
  .col-room-options::before { content: "Room Options"; }
  .col-description::before { content: "Description"; }
  .col-cancellation::before { content: "Cancellation Policy"; }
  .col-price::before { content: "Price"; }

  .facilities-grid {
    grid-template-columns: 1fr;
  }

  .location-details {
    grid-template-columns: 1fr;
  }

  .review-score-large {
    flex-direction: column;
    text-align: center;
  }

  .score-circle-large {
    width: 80px;
    height: 80px;
    font-size: 2rem;
  }

  .score-description-large {
    font-size: 1.25rem;
  }

  .category-score-item {
    grid-template-columns: 100px 1fr;
    gap: 0.75rem;
  }
}

@media (max-width: 480px) {
  .tab-button {
    min-width: 100px;
    font-size: 0.85rem;
    padding: 0.75rem 0.5rem;
    gap: 0.25rem;
  }

  .tab-content {
    padding: 1rem;
  }

  .category-score-item {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }

  .score-bar-container {
    gap: 0.5rem;
  }
}