.search-flights-container {
  /* min-height: 75vh; */
  width: 100%;
  position: relative;
  overflow: hidden;
  top: 170px;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
}

@media (max-width: 768px) {
  .search-flights-container {
    top: 120px;
  }
}

@media (max-width: 480px) {
  .search-flights-container {
    top: 100px;
  }
}

.search-flights-background {
  position: relative;
  /* height: 73vh; */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  /* animation: backgroundZoom 20s ease-in-out infinite alternate; */
}

@media (max-width: 768px) {
  .search-flights-background {
    height: auto;
    min-height: 100vh;
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .search-flights-background {
    padding: 0.5rem;
  }
}

@media (orientation: landscape) and (max-height: 600px) {
  .search-flights-background {
    height: auto;
    min-height: 100%;
    padding: 1rem;
  }
}

@keyframes backgroundZoom {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.05);
  }
}

