/* Recent Searches */
.recent-searches-section {
  background: linear-gradient(180deg, #ffffff 0%, #f8fafc 100%);
  padding: 36px 0 20px 0;
  border-top: 1px solid #e5e7eb;
}

.recent-searches-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.recent-searches-header {
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.recent-searches-heading {
  font-size: 1.6rem;
  font-weight: 800;
  color: #0f172a;
  letter-spacing: 0.2px;
  text-align: left;
}

.recent-searches-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 16px;
}

.recent-searches-item {
  -webkit-appearance: none;
  appearance: none;
  background: linear-gradient(180deg, #ffffff 0%, #f8fafc 100%);
  border: 1px solid #e5e7eb;
  border-radius: 14px;
  padding: 14px 16px;
  text-align: left;
  cursor: pointer;
  transition: box-shadow 0.25s ease, transform 0.15s ease, border-color 0.25s ease;
  box-shadow: 0 1px 2px rgba(0,0,0,0.04);
}

.recent-searches-item:hover {
  box-shadow: 0 10px 24px rgba(0,0,0,0.10), 0 2px 6px rgba(0,0,0,0.06);
  transform: translateY(-2px);
  border-color: #d1d5db;
}

.recent-searches-item:focus-visible {
  outline: none;
  box-shadow: 0 0 0 4px rgba(59,130,246,0.18), 0 8px 22px rgba(0,0,0,0.08);
  border-color: #93c5fd;
}

.recent-searches-top {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 6px;
}

.recent-searches-trip {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: #fff;
  border-radius: 999px;
  padding: 3px 10px;
  font-size: 0.7rem;
  font-weight: 800;
  letter-spacing: 0.3px;
}

.recent-searches-cabin {
  background: #eef2ff;
  color: #3730a3;
  border-radius: 999px;
  padding: 3px 10px;
  font-size: 0.7rem;
  font-weight: 700;
}

.recent-searches-tag {
  background: #ecfdf5;
  color: #065f46;
  border-radius: 999px;
  padding: 3px 10px;
  font-size: 0.7rem;
  font-weight: 700;
}

.recent-searches-airline {
  background: #fff7ed;
  color: #9a3412;
  border-radius: 999px;
  padding: 3px 10px;
  font-size: 0.7rem;
  font-weight: 700;
}

.recent-searches-routes {
  font-size: 0.95rem;
  color: #0f172a;
  margin-bottom: 4px;
  line-height: 1.4;
}

.recent-searches-meta {
  font-size: 0.82rem;
  color: #64748b;
}

@media (max-width: 768px) {
  .recent-searches-list {
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 12px;
  }

  .recent-searches-heading {
    font-size: 1.3rem;
  }
}

@keyframes rs-fade-in {
  from { opacity: 0; transform: translateY(4px); }
  to { opacity: 1; transform: translateY(0); }
}

.recent-searches-item {
  animation: rs-fade-in 240ms ease both;
}

