.hb-page {
  background: linear-gradient(135deg, #f5f7fb 0%, #e8edf5 100%);
  min-height: 100vh;
  padding: 24px 0 80px;
}

.hb-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Professional Loading Banner */
.hb-loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 200px);
  padding: 40px 20px;
}

.hb-loading-card {
  background: #ffffff;
  border-radius: 20px;
  padding: 60px 40px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1), 0 2px 8px rgba(0, 0, 0, 0.05);
  text-align: center;
  max-width: 500px;
  width: 100%;
  animation: hb-fade-in 0.4s ease-out;
}

@keyframes hb-fade-in {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hb-loading-spinner {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto 32px;
}

.hb-spinner-ring {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 4px solid transparent;
  border-top-color: #3b82f6;
  border-radius: 50%;
  animation: hb-spin 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
}

.hb-spinner-ring:nth-child(1) {
  animation-delay: -0.45s;
  border-top-color: #3b82f6;
  opacity: 1;
}

.hb-spinner-ring:nth-child(2) {
  animation-delay: -0.3s;
  border-top-color: #60a5fa;
  opacity: 0.7;
  width: 70%;
  height: 70%;
  top: 15%;
  left: 15%;
}

.hb-spinner-ring:nth-child(3) {
  animation-delay: -0.15s;
  border-top-color: #93c5fd;
  opacity: 0.5;
  width: 50%;
  height: 50%;
  top: 25%;
  left: 25%;
}

@keyframes hb-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

