.hb-info-subtitle {
  margin-top: 12px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 500;
}

.hb-info-text {
  margin-bottom: 16px;
  font-size: 14px;
  line-height: 1.6;
}

.hb-info-title {
  margin-bottom: 12px;
  font-size: 16px;
  font-weight: 600;
}

.hb-info-list {
  margin: 0;
  padding-left: 20px;
  list-style-type: disc;
  font-size: 14px;
  line-height: 1.8;
}

/* Error and Success Messages */
.hb-error-message {
  padding: 12px;
  margin-bottom: 16px;
  background: #fee;
  color: #c00;
  border-radius: 4px;
  font-size: 14px;
}

.hb-success-message {
  padding: 12px;
  margin-bottom: 16px;
  background: #efe;
  color: #0a0;
  border-radius: 4px;
  font-size: 14px;
}

/* Review Modal Styles */
.hb-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: 20px;
  overflow-y: auto;
  backdrop-filter: blur(4px);
}

.hb-modal-content {
  background: #ffffff;
  border-radius: 16px;
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: hb-modal-slide-in 0.3s ease-out;
}

@keyframes hb-modal-slide-in {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.hb-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 28px;
  border-bottom: 2px solid #e2e8f0;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  border-radius: 16px 16px 0 0;
}

.hb-modal-title {
  font-size: 24px;
  font-weight: 800;
  color: #0f172a;
  margin: 0;
  letter-spacing: -0.3px;
}

.hb-modal-close {
  background: none;
  border: none;
  font-size: 32px;
  color: #6b7280;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  transition: all 0.2s ease;
  line-height: 1;
}

.hb-modal-close:hover:not(:disabled) {
  background: #f1f5f9;
  color: #1f2937;
}

.hb-modal-close:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.hb-modal-body {
  padding: 28px;
  overflow-y: auto;
  flex: 1;
}

.hb-modal-section {
  margin-bottom: 28px;
  padding-bottom: 24px;
  border-bottom: 1px solid #e2e8f0;
}

.hb-modal-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.hb-modal-section-title {
  font-size: 18px;
  font-weight: 700;
  color: #0f172a;
  margin-bottom: 16px;
  letter-spacing: -0.2px;
}

.hb-modal-info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.hb-modal-label {
  display: block;
  font-size: 12px;
  color: #6b7280;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 4px;
  letter-spacing: 0.5px;
}

.hb-modal-value {
  display: block;
  font-size: 14px;
  color: #1f2937;
  font-weight: 500;
  word-break: break-word;
}

.hb-modal-room-guest {
  margin-bottom: 20px;
  padding: 16px;
  background: #f8fafc;
  border-radius: 12px;
  border: 1px solid #e2e8f0;
}

.hb-modal-room-guest:last-child {
  margin-bottom: 0;
}

.hb-modal-room-title {
  font-size: 16px;
  font-weight: 700;
  color: #0f172a;
  margin-bottom: 12px;
  padding-bottom: 8px;
  border-bottom: 1px solid #cbd5e1;
}

.hb-modal-guest-item {
  margin-bottom: 16px;
  padding: 12px;
  background: #ffffff;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.hb-modal-guest-item:last-child {
  margin-bottom: 0;
}

.hb-modal-guest-name {
  font-size: 14px;
  font-weight: 600;
  color: #3b82f6;
  margin-bottom: 12px;
}

.hb-modal-special-request {
  padding: 16px;
  background: #f8fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
  font-size: 14px;
  color: #374151;
  line-height: 1.6;
  white-space: pre-wrap;
}

.hb-modal-pricing {
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  padding: 20px;
  border-radius: 12px;
  border: 2px solid #cbd5e1;
}

.hb-modal-price-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px dashed #cbd5e1;
}

.hb-modal-price-row:last-child {
  border-bottom: none;
}

.hb-modal-price-total {
  border-top: 2px solid #cbd5e1;
  margin-top: 8px;
  padding-top: 16px;
  padding-bottom: 0;
}

.hb-modal-price-total .hb-modal-label {
  font-size: 16px;
  font-weight: 700;
  color: #0f172a;
}

.hb-modal-price-total .hb-modal-value {
  font-size: 20px;
  font-weight: 800;
  color: #1f2937;
}

.hb-modal-footer {
  display: flex;
  gap: 12px;
  padding: 24px 28px;
  border-top: 2px solid #e2e8f0;
  background: #f8fafc;
  border-radius: 0 0 16px 16px;
}

.hb-modal-btn {
  flex: 1;
  padding: 14px 24px;
  border-radius: 10px;
  font-weight: 700;
  font-size: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.hb-modal-btn-cancel {
  background: #ffffff;
  color: #6b7280;
  border: 2px solid #cbd5e1;
}

.hb-modal-btn-cancel:hover:not(:disabled) {
  background: #f1f5f9;
  color: #1f2937;
  border-color: #94a3b8;
}

.hb-modal-btn-confirm {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: #ffffff;
  box-shadow: 0 4px 6px rgba(37, 99, 235, 0.3);
}

.hb-modal-btn-confirm:hover:not(:disabled) {
  background: linear-gradient(135deg, #2563eb 0%, #1d4ed8 100%);
  box-shadow: 0 6px 12px rgba(37, 99, 235, 0.4);
  transform: translateY(-2px);
}

.hb-modal-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

@media (max-width: 960px) {
  .hb-loading-card {
    padding: 50px 30px;
    max-width: 90%;
  }

  .hb-loading-title {
    font-size: 20px;
  }

  .hb-loading-subtitle {
    font-size: 14px;
  }

  .hb-loading-spinner {
    width: 70px;
    height: 70px;
    margin-bottom: 28px;
  }

  .hb-layout {
    grid-template-columns: 1fr;
  }
  
  .hb-right {
    position: static;
  }
  
  .hb-form {
    grid-template-columns: 1fr;
  }

  .hb-form-with-pan,
  .hb-form-no-pan {
    grid-template-columns: 1fr;
  }

  .hb-cancellation-header,
  .hb-cancellation-row {
    grid-template-columns: 1fr;
  }

  .hb-phone {
    grid-template-columns: 1fr;
  }

  .hb-modal-content {
    max-width: 100%;
    max-height: 95vh;
    margin: 10px;
  }

  .hb-modal-header {
    padding: 20px;
  }

  .hb-modal-title {
    font-size: 20px;
  }

  .hb-modal-body {
    padding: 20px;
  }

  .hb-modal-info-grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .hb-modal-footer {
    flex-direction: column;
    padding: 20px;
  }

  .hb-modal-btn {
    width: 100%;
  }
}
