.date-range-picker-container {
  position: relative;
  width: 100%;
}

.date-range-picker-inputs-wrapper {
  display: flex;
  align-items: stretch;
  width: 100%;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  background: white;
  overflow: hidden;
  transition: all 0.3s ease;
  font-family: 'Arial', sans-serif;
}

.date-range-picker-inputs-wrapper:hover {
  border-color: #3b82f6;
}

.date-range-picker-field-wrapper {
  flex: 1;
  padding: 0.875rem 1rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0;
  transition: background 0.2s ease;
}

.date-range-picker-field-wrapper:hover {
  background: #f9fafb;
}

.date-range-picker-field-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.65rem;
  font-weight: 500;
  color: #9ca3af;
  text-transform: none;
  letter-spacing: 0;
  line-height: 1;
  margin-bottom: 0.0625rem;
}

.date-range-picker-field-icon {
  color: #9ca3af;
  font-size: 0.7rem;
}

.date-range-picker-field-value {
  font-size: 1rem;
  font-weight: 700;
  color: #1f2937;
  line-height: 1;
  margin-top: 0.0625rem;
}

.date-range-picker-separator {
  width: 1px;
  background: #e5e7eb;
  align-self: stretch;
  margin: 0.5rem 0;
}

.date-range-picker-calendar {
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  background: white;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  padding: 1rem;
  min-width: 320px;
  width: max-content;
}

/* Summary Header */
.date-range-picker-summary {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #e5e7eb;
}

.date-range-picker-summary-item {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  align-items: flex-start;
}

.date-range-picker-summary-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.date-range-picker-summary-date {
  font-size: 0.9rem;
  font-weight: 600;
  color: #1f2937;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.date-range-picker-check-icon {
  color: #10b981;
  font-size: 1rem;
}

/* Calendar Navigation */
.date-range-picker-calendar-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.75rem;
}

.date-range-picker-nav-button {
  background: transparent;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #374151;
  transition: all 0.2s ease;
}

.date-range-picker-nav-button:hover {
  background: #f3f4f6;
  border-color: #d1d5db;
}

.date-range-picker-month-year {
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
}

/* Calendar Grid */
.date-range-picker-calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  overflow: hidden;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.date-range-picker-day.dragging {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.date-range-picker-day-header {
  padding: 0.5rem;
  text-align: center;
  font-size: 0.7rem;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  background: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}

.date-range-picker-day-header:not(:last-child) {
  border-right: 1px solid #e5e7eb;
}

.date-range-picker-day-empty {
  padding: 0.5rem;
  background: white;
}

.date-range-picker-day {
  padding: 0.5rem;
  text-align: center;
  font-size: 0.85rem;
  color: #1f2937;
  cursor: pointer;
  transition: all 0.2s ease;
  background: white;
  border-right: 1px solid #e5e7eb;
  border-bottom: 1px solid #e5e7eb;
  position: relative;
}

.date-range-picker-day:nth-child(7n) {
  border-right: none;
}

.date-range-picker-day:hover:not(.disabled) {
  background: #f3f4f6;
}

.date-range-picker-day.disabled {
  color: #9ca3af;
  cursor: not-allowed;
  background: #f9fafb;
  opacity: 0.5;
}

.date-range-picker-day.in-range {
  background: #dbeafe;
  color: #1f2937;
}

.date-range-picker-day.checkin {
  background: #3b82f6;
  color: white;
  font-weight: 600;
}

.date-range-picker-day.checkin:not(.checkout) {
  border-radius: 6px 0 0 6px;
}

.date-range-picker-day.checkout {
  background: #3b82f6;
  color: white;
  font-weight: 600;
}

.date-range-picker-day.checkout:not(.checkin) {
  border-radius: 0 6px 6px 0;
}

.date-range-picker-day.selected {
  background: #3b82f6;
  color: white;
  font-weight: 600;
}

.date-range-picker-day.selected.in-range {
  background: #3b82f6;
  color: white;
}

/* When both checkin and checkout are the same date */
.date-range-picker-day.checkin.checkout {
  border-radius: 6px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .date-range-picker-calendar {
    left: 0;
    right: 0;
    min-width: auto;
    width: 100%;
  }

  .date-range-picker-summary {
    flex-direction: column;
    gap: 1rem;
  }

  .date-range-picker-day {
    padding: 0.5rem;
    font-size: 0.85rem;
  }
}
