/* Main container */
.search-results-container {
  padding: 20px;
  background-color: #f8f9fa;
  min-height: 100vh;
  padding-bottom: 120px;
  /* Add space for fixed footer */
  max-width: 100%;
  margin: 0 auto;
}

/* Responsive Design System */
/* Large screens (22-inch and above) */
@media (min-width: 1921px) {
  .search-results-container {
    max-width: 1800px;
    padding: 24px;
  }
}

/* Medium-large screens (17-21 inch) */
@media (min-width: 1600px) and (max-width: 1920px) {
  .search-results-container {
    max-width: 1600px;
    padding: 20px;
  }
}

/* Medium screens (15-16 inch) */
@media (min-width: 1366px) and (max-width: 1599px) {
  .search-results-container {
    max-width: 1400px;
    padding: 18px;
  }
}

/* Small-medium screens (13-14 inch) */
@media (min-width: 1024px) and (max-width: 1365px) {
  .search-results-container {
    max-width: 1200px;
    padding: 16px;
  }
}

/* Small screens (11-12 inch) */
@media (min-width: 768px) and (max-width: 1023px) {
  .search-results-container {
    max-width: 100%;
    padding: 14px;
  }
}

/* Mobile screens */
@media (max-width: 767px) {
  .search-results-container {
    padding: 12px;
    padding-bottom: 140px;
  }
}

