/* Progress indicator styles */

.segment-tabs {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  /* justify-content: center; */
}

.segment-tab {
  background: #f8f9fa;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  padding: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 200px;
  text-align: left;
  position: relative;
}

.segment-tab:hover {
  border-color: #667eea;
  background: #f0f2ff;
}

.segment-tab.active {
  border-color: #667eea;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.segment-tab.selected-flight {
  border-color: #28a745;
  background: linear-gradient(135deg, #f0f2ff 0%, #e8f0ff 100%);
  position: relative;
}

.segment-tab.selected-flight::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
  border-radius: 10px;
  z-index: -1;
}

