  margin-bottom: 8px;
} */

.segment-cities {
  font-size: 16px;
  font-weight: 600;
}

.segment-date {
  font-size: 14px;
  opacity: 0.9;
  color: #1add4b;
}

.segment-airports {
  font-size: 12px;
  opacity: 0.8;
  font-family: monospace;
