/* Multi-city Container Styles */
.multi-city-container {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
  overflow: hidden;
}

.multi-city-error {
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 8px;
  padding: 16px;
  margin: 20px 0;
  text-align: center;
  color: #856404;
}

/* Multi-city Summary Styles */
.multi-city-summary {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
  padding: 24px;
}

.summary-header {
  text-align: center;
  margin-bottom: 24px;
}

.summary-header h3 {
  margin: 0 0 8px 0;
  font-size: 20px;
  font-weight: 600;
  color: #333;
}

.summary-header p {
  margin: 0;
  color: #666;
  font-size: 14px;
}

.summary-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 24px;
}

.summary-stat {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.stat-icon {
  font-size: 24px;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 50%;
}

.stat-content {
  flex: 1;
}

.stat-label {
  font-size: 12px;
  color: #666;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 4px;
}

.stat-value {
  font-size: 18px;
  font-weight: 600;
  color: #333;
}

.segment-summary-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.segment-summary-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
  transition: all 0.3s ease;
}

.segment-summary-item:hover {
  border-color: #667eea;
  background: #f0f2ff;
}

.segment-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.segment-number {
  font-size: 12px;
  color: #667eea;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.segment-route {
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.segment-pricing {
  display: flex;
  flex-direction: column;
  gap: 8px;
  text-align: right;
}

.price-info,
.duration-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.price-label,
.duration-label {
  font-size: 11px;
  color: #666;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.price-value {
  font-size: 16px;
  font-weight: 600;
  color: #28a745;
}

.duration-value {
  font-size: 14px;
  color: #333;
}

/* Round Trip Layout Styles */
.round-trip-container {
  margin: 20px 0;
  width: 100%;
  position: relative;
}

.round-trip-columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  margin: 0 auto;
  width: 100%;
  position: relative;
}

