.flight-column {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  min-width: 0;
  /* Allow shrinking */
  transition: all 0.3s ease;
}

.flight-column:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.column-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 20px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

/* Add subtle animation to header background */
.column-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 2s ease;
}

.column-header:hover::before {
  left: 100%;
}

.column-header h3 {
  margin: 0 0 12px 0;
  font-size: 20px;
  font-weight: 600;
  position: relative;
  z-index: 1;
}

.route-inf {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 16px;
  font-weight: 500;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;
}

.flights-list {
  padding: 20px;
  min-height: 90vh;
  overflow-y: auto;
  scrollbar-width: none;
  scrollbar-color: #c1c1c1 #f1f1f1;
}

/* Custom scrollbar for flights list */
.flights-list::-webkit-scrollbar {
  width: 6px;
}

.flights-list::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.flights-list::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.flights-list::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

.flight-column.outbound .column-header {
  background: linear-gradient(135deg, #2da8d8 0%, #667eea 100%);
}

.flight-column.return .column-header {
  background: linear-gradient(135deg, #667eea 0%, #2da8d8 100%);
}

.round-trip-error {
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
  text-align: center;
  color: #856404;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* Responsive optimizations for round-trip flight cards */
@media (max-width: 1200px) {
  .round-trip-columns .flight-card {
    padding: 20px;
    gap: 16px;
    margin: 16px 0;
  }

  .round-trip-columns .flight-card-left {
    flex: 0 0 160px;
  }

  .round-trip-columns .flight-card-right {
    flex: 0 0 240px;
  }
}

@media (max-width: 1024px) {
  .round-trip-columns .flight-card {
    padding: 18px;
    gap: 14px;
    margin: 14px 0;
  }

  .round-trip-columns .flight-card-left {
    flex: 0 0 140px;
  }

  .round-trip-columns .flight-card-right {
    flex: 0 0 220px;
  }

  .round-trip-columns .airline-logo {
    width: 32px;
    height: 32px;
  }

  .round-trip-columns .airport-code {
    font-size: 15px;
  }

  .round-trip-columns .time {
    font-size: 16px;
  }
}

@media (max-width: 767px) {
  .round-trip-columns .flight-card {
    padding: 16px;
    margin: 12px 0;
    flex-direction: column;
    gap: 12px;
  }

  .round-trip-columns .flight-card-left,
  .round-trip-columns .flight-card-right {
    flex: none;
    width: 100%;
  }

  .round-trip-columns .flight-card-center {
    padding: 12px 0;
    order: -1;
  }

  .round-trip-columns .departure-info,
  .round-trip-columns .arrival-info {
    flex-direction: row;
    gap: 12px;
    text-align: left;
  }

  .round-trip-columns .flight-path {
    flex-direction: row;
    max-width: none;
    justify-content: center;
    gap: 12px;
  }

  .round-trip-columns .flight-arrow {
    transform: rotate(90deg);
    font-size: 20px;
  }
}

@media (max-width: 480px) {
  .round-trip-columns .flight-card {
    padding: 12px;
    margin: 10px 0;
    border-radius: 8px;
  }

  .round-trip-columns .airline-logo {
    width: 28px;
    height: 28px;
  }

  .round-trip-columns .airport-code {
    font-size: 14px;
  }

  .round-trip-columns .time {
    font-size: 14px;
  }

  .round-trip-columns .book-button {
    padding: 10px 16px;
    font-size: 12px;
  }
}

.single-column-flights {
  margin: 0 auto;
  width: 100%;
}

/* Additional round-trip responsive enhancements */
/* Smooth transitions between breakpoints */
.round-trip-columns,
.flight-column,
.column-header,
.flights-list {
  transition: all 0.3s ease;
}

/* Ensure optimal touch targets on mobile */
@media (max-width: 767px) {

  .round-trip-columns .flight-card .view-details-btn,
  .round-trip-columns .flight-card .book-button,
  .round-trip-columns .flight-card .fare-option {
    min-height: 44px;
    touch-action: manipulation;
  }

  .round-trip-columns .flight-card .fare-radio input[type="radio"] {
    width: 20px;
    height: 20px;
  }

  /* Improve readability on small screens */
  .round-trip-columns .flight-card .airline-name {
    font-size: 14px;
    line-height: 1.3;
  }

  .round-trip-columns .flight-card .flight-number {
    font-size: 11px;
  }

  .round-trip-columns .flight-card .date {
    font-size: 11px;
  }

  .round-trip-columns .flight-card .duration {
    font-size: 11px;
  }

  .round-trip-columns .flight-card .stop-label {
    font-size: 10px;
    padding: 2px 8px;
  }
}

/* Landscape orientation optimizations for mobile */
@media (max-width: 767px) and (orientation: landscape) {
  .round-trip-columns {
    grid-template-columns: 1fr 1fr;
    gap: 12px;
  }

  .round-trip-columns::before {
    display: flex !important;
    width: 32px;
    height: 32px;
    font-size: 16px;
  }

  .column-header {
    padding: 12px;
  }

  .column-header h3 {
    font-size: 16px;
    margin-bottom: 6px;
  }

  .route-inf {
    font-size: 12px;
    flex-direction: row;
    gap: 4px;
  }

  .flights-list {
    padding: 12px;
    max-height: 30vh;
  }

  .flight-column.return::before {
    display: none;
  }

  .flight-column.return {
    margin-top: 0;
  }
}

/* Enhanced accessibility and focus states */
.round-trip-columns .flight-card:focus-within {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}

.round-trip-columns .column-header:focus-within {
  outline: 2px solid rgba(255, 255, 255, 0.5);
  outline-offset: 2px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .round-trip-columns::before {
    border-width: 4px;
    font-weight: 900;
  }

  .column-header {
    border: 2px solid rgba(255, 255, 255, 0.3);
  }

  .flight-column {
    border: 2px solid #000;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {

  .round-trip-columns,
  .flight-column,
  .column-header,
  .flights-list,
  .column-header::before {
    transition: none;
    animation: none;
  }

  .flight-column:hover {
    transform: none;
  }

  .column-header:hover::before {
    display: none;
  }
}

