/* Search Details Bar - Dark grey horizontal bar */
.search-details-bar {
  background: #2c3e50;
  padding: 16px 24px;
  margin-bottom: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 16px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  width: 100%;
}

/* Responsive design for search details bar */
/* Large screens (22-inch and above) */
