/* Route information container */
.route-info {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
  min-width: 0;
  overflow-x: auto;
  overflow-y: hidden;
  scrollbar-width: thin;
  scrollbar-color: #34495e #2c3e50;
  padding-bottom: 4px;
}

/* Custom scrollbar styling for webkit browsers */
.route-info::-webkit-scrollbar {
  height: 6px;
}

.route-info::-webkit-scrollbar-track {
  background: #34495e;
  border-radius: 3px;
}

.route-info::-webkit-scrollbar-thumb {
  background: #ecf0f1;
  border-radius: 3px;
}

.route-info::-webkit-scrollbar-thumb:hover {
  background: #bdc3c7;
}

/* City code and name */
.city-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-width: 80px;
}

.city-code {
  font-size: 18px;
  font-weight: bold;
  color: #ecf0f1;
  line-height: 1.2;
}

.city-name {
  font-size: 12px;
  color: #bdc3c7;
  line-height: 1.2;
  margin-top: 2px;
}

/* Flight icon */
.flight-icon {
  color: #ecf0f1;
  font-size: 16px;
  margin: 0 8px;
  flex-shrink: 0;
}

/* Date information */
.date-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-width: 60px;
  margin-left: 8px;
  flex-shrink: 0;
}

.date-value {
  font-size: 14px;
  font-weight: bold;
  color: #ecf0f1;
  line-height: 1.2;
  background: rgba(255, 255, 255, 0.1);
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Vertical separator */
.separator {
  width: 1px;
  height: 40px;
  background-color: #34495e;
  margin: 0 16px;
  flex-shrink: 0;
}

/* Search parameters */
.search-params {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
}

.param-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
