}

.param-label {
  font-size: 11px;
  color: #bdc3c7;
  line-height: 1.2;
  margin-bottom: 2px;
}

.param-value {
  font-size: 14px;
  font-weight: bold;
  color: #ecf0f1;
  line-height: 1.2;
}

/* Modify Search Button */
.modify-search-btn {
  background: transparent;
  color: #ecf0f1;
  border: 1px solid #ecf0f1;
  border-radius: 6px;
  padding: 8px 16px;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: all 0.2s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.modify-search-btn:hover {
  background: #ecf0f1;
  color: #2c3e50;
}

.modify-search-btn .chevron {
  font-size: 10px;
  transition: transform 0.2s ease;
}

.modify-search-btn:hover .chevron {
