/* Main Calendar Container */
.flight-calendar-container {
  background: white;
  padding: 20px;
  margin-top: 3%;
  margin-bottom: -4.3%;
}

/* Calendar Grid with Side Navigation */
.calendar-navigation-container {
  display: flex;
  align-items: center;
  gap: 16px;
}

/* Navigation Arrow Buttons */
.calendar-nav-button {
  background: transparent;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  font-weight: bold;
  flex-shrink: 0;
  color: #495057;
}

.calendar-nav-button:hover:not(:disabled) {
  background: #f8f9fa;
}

.calendar-nav-button:disabled {
  color: #6c757d;
  cursor: not-allowed;
}

/* Calendar Grid */
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0;
  flex: 1;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  overflow: hidden;
}

/* Individual Date Column */
.calendar-date-column {
  padding: 16px 8px;
  background: white;
  border-right: 1px solid #dee2e6;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
}

.calendar-date-column:last-child(7n) {
  border-right: none;
}

.calendar-date-column:hover {
  background: #f8f9fa;
}

.calendar-date-column.selected {
  background: white;
  color: #3564ff;
  border-bottom: 3px solid #3564ff;
  /* border-top: 3px solid #3564ff; */
}

/* Date Text */
.calendar-date-text {
  font-size: 13px;
  margin-bottom: 8px;
  font-weight: 500;
  line-height: 1.2;
  color: #495057;
}

.calendar-date-column.selected .calendar-date-text {
  color: #3564ff;
}

/* Past Date Styling */
.calendar-date-column.past-date {
  background: #f8f9fa;
  color: #adb5bd;
  cursor: not-allowed;
  opacity: 0.6;
}

.calendar-date-column.past-date:hover {
  background: #f8f9fa;
  transform: none;
  box-shadow: none;
  background: white;
}

.calendar-date-column.past-date .calendar-date-text,
.calendar-date-column.past-date .calendar-fare-text,
.calendar-date-column.past-date .calendar-loading,
.calendar-date-column.past-date .calendar-error,
.calendar-date-column.past-date .calendar-fetch-fare {
  color: #adb5bd;
}


/* Fare Text */
.calendar-fare-text {
  font-size: 16px;
  font-weight: bold;
  color: #495057;
}

.calendar-date-column.selected .calendar-fare-text {
  color: #3564ff;
}

/* Loading State */
.calendar-loading {
  font-size: 12px;
  color: #6c757d;
  opacity: 0.7;
}

.calendar-date-column.selected .calendar-loading {
  color: #3564ff;
}

/* Error State */
.calendar-error {
  font-size: 11px;
  color: #dc3545;
  opacity: 0.8;
}

.calendar-date-column.selected .calendar-error {
  color: #3564ff;
}

/* Fetch Fare State */
.calendar-fetch-fare {
  font-size: 12px;
  color: #6c757d;
  font-weight: 600;
}

.calendar-date-column.selected .calendar-fetch-fare {
  color: #3564ff;
}

/* Responsive Design */
@media (max-width: 768px) {
  .calendar-navigation-container {
    gap: 12px;
  }

  .calendar-nav-button {
    width: 36px;
    height: 36px;
