/* Flight Card Styles */
.flight-card {
  background: #ffffff;
  border: 1px solid #e1e5e9;
  border-radius: 16px;
  margin: 20px 0;
  padding: 24px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
  display: flex;
  gap: 24px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

/* Segment Indicator */
.segment-indicator {
  position: absolute;
  top: 16px;
  right: 16px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  z-index: 1;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
}

.flight-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  /* background: linear-gradient(90deg, #ff6b35, #ff8c42); */
  opacity: 0;
  transition: opacity 0.3s ease;
}

.flight-card:hover::before {
  opacity: 1;
}

.flight-card:hover {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.12);
  transform: translateY(-2px);
  border-color: #2da8d8;
}

/* Left Section - Airline Information and Actions */
.flight-card-left {
  flex: 0 0 200px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  position: relative;
}

.airline-info {
  display: flex;
  align-items: center;
  gap: 12px;
  /* padding: 8px; */
  border-radius: 8px;
  transition: background-color 0.2s ease;
}

.airline-info:hover {
  background: #f8f9fa;
}

.airline-logo {
  width: 40px;
  height: 40px;
  object-fit: contain;
  border-radius: 8px;
  border: 1px solid #f0f0f0;
  transition: transform 0.2s ease;
}

.airline-logo:hover {
  transform: scale(1.05);
}

.airline-details {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.airline-name {
  font-size: 16px;
  font-weight: 600;
  color: #1575d6;
  line-height: 1.2;
}

.flight-numbers {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.flight-number {
  font-size: 12px;
  color: #666;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 4px;
}

.flight-separator {
  color: #ccc;
  font-size: 10px;
  margin-left: 2px;
}

.view-details-btn {
  background: #f8f9fa;
  color: #35a4ff;
  border: 1px solid #35a4ff;
  border-radius: 8px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  transition: all 0.2s ease;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.view-details-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.view-details-btn:hover::before {
  left: 100%;
}

.view-details-btn:hover {
  background: #35b5ff;
  color: white;
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(255, 107, 53, 0.3);
}

.plus-icon {
  font-size: 16px;
  font-weight: bold;
}

.arrival-day-info {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: #2c3e50;
  padding: 6px 12px;
  background: #e3f2fd;
  border-radius: 12px;
  border: 1px solid #bbdefb;
  animation: pulse 2s infinite;
}

@keyframes pulse {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.8;
  }
}

.airplane-icon {
  color: #1976d2;
  font-size: 14px;
  animation: fly 3s infinite;
}

@keyframes fly {

  0%,
  100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(3px);
  }
}

.seats-warning {
  /* background: #fff3e0; */
  color: #ff6b35;
  font-size: 13px;
  font-weight: 600;
  padding: 6px 12px;
  border-radius: 12px;
  /* border: 1px solid #ffcc02; */
  text-align: center;
  /* animation: shake 2s infinite; */
}

/* Red warning for 1-3 seats left */
.seats-warning-red {
  background: #ffebee;
  color: #d32f2f;
  border: 1px solid #ffcdd2;
}

/* Orange warning for 4-6 seats left */
.seats-warning-orange {
  background: #fff3e0;
  color: #f57c00;
  border: 1px solid #ffcc02;
}

/* Green warning for 7+ seats left */
.seats-warning-green {
  background: #e8f5e8;
  color: #2e7d32;
  border: 1px solid #c8e6c9;
}

@keyframes shake {

  0%,
  100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-2px);
  }

  75% {
    transform: translateX(2px);
  }
}

.nearby-airport-warning {
  background: #fff;
  color: #062cd4;
  font-size: 12px;
  font-weight: 400;
  padding: 6px 12px;
  border-radius: 12px;
  border: 1px solid #4f97ad;
  text-align: center;
  animation: pulse 2s infinite;
}

/* Center Section - Flight Route and Timing */
.flight-card-center {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  position: relative;
}

.flight-card-center::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, #e9ecef, transparent);
  transform: translateY(-50%);
}

.departure-info,
.arrival-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  text-align: center;
  position: relative;
  z-index: 1;
}

.airport-code {
  font-size: 17px;
  font-weight: 700;
  /* color: #2c3e50; */
  letter-spacing: 1px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.time {
  font-size: 18px;
  font-weight: 600;
  color: #2c3e50;
}

.date {
  font-size: 13px;
  color: #7f8c8d;
  font-weight: 500;
}

/* COMBO Flights Styles */
.combo-flights-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}

.combo-journey {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.journey-label {
  font-size: 12px;
  font-weight: 600;
  color: #667eea;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  padding: 4px 0;
  background: linear-gradient(90deg, #667eea 0%, transparent 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.journey-route {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.combo-flight-group {
  display: flex;
  align-items: center;
  gap: 4px;
  flex-wrap: wrap;
  margin-top: 4px;
}

.combo-label {
  font-size: 11px;
  font-weight: 600;
  color: #667eea;
  text-transform: uppercase;
}

.combo-flight-group .flight-number {
  font-size: 11px;
  display: inline-flex;
  align-items: center;
  gap: 4px;
}

.combo-flight-group .flight-separator {
  margin: 0 2px;
  color: #adb5bd;
}

.flight-path {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  flex: 1;
  max-width: 120px;
  position: relative;
  z-index: 1;
}

.stop-label {
  font-size: 12px;
  color: #7f8c8d;
  font-weight: 500;
  text-align: center;
  background: #f8f9fa;
  padding: 4px 12px;
  border-radius: 12px;
  border: 1px solid #e9ecef;
  transition: all 0.2s ease;
  position: relative;
  cursor: help;
}

.stop-label:hover {
  background: #e9ecef;
  border-color: #ff6b35;
  color: #ff6b35;
}

/* Stopover Tooltip Styles */
.stopover-tooltip {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: #2c3e50;
  color: white;
  padding: 12px;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  min-width: 200px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  z-index: 1000;
  margin-bottom: 8px;
  pointer-events: none;
}

.stopover-tooltip::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 6px solid transparent;
  border-top-color: #2c3e50;
}

.stop-label:hover .stopover-tooltip {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) translateY(-4px);
}

.tooltip-header {
  font-size: 11px;
  font-weight: 600;
  color: #bdc3c7;
  margin-bottom: 6px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tooltip-stopover {
  font-size: 12px;
  color: #ecf0f1;
  padding: 2px 0;
  border-bottom: 1px solid #34495e;
  display: flex;
  align-items: center;
  gap: 6px;
}

.tooltip-stopover:last-child {
  border-bottom: none;
}

.tooltip-stopover::before {
  content: '';
  font-size: 10px;
  color: #f39c12;
}

/* Responsive adjustments for tooltip */
@media (max-width: 768px) {
  .stopover-tooltip {
    min-width: 180px;
    padding: 10px;
  }

  .tooltip-header {
    font-size: 10px;
  }

  .tooltip-stopover {
    font-size: 11px;
  }
}

@media (max-width: 480px) {
  .stopover-tooltip {
    min-width: 160px;
    padding: 8px;
    margin-bottom: 6px;
  }

  .tooltip-header {
    font-size: 9px;
    margin-bottom: 4px;
  }

  .tooltip-stopover {
    font-size: 10px;
    padding: 1px 0;
  }
}

.flight-arrow {
  font-size: 24px;
  color: #bdc3c7;
  font-weight: bold;
  transition: transform 0.3s ease;
}

.flight-card:hover .flight-arrow {
  color: #ff6b35;
  transform: scale(1.1);
}

.duration {
  font-size: 13px;
  color: #7f8c8d;
  font-weight: 500;
  text-align: center;
}

/* Right Section - Fare Options and Booking */
.flight-card-right {
  flex: 0 0 280px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.fare-options {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.fare-option {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  background: #fafbfc;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.fare-option::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: #ff6b35;
  transform: scaleY(0);
  transition: transform 0.2s ease;
}

.fare-option:hover::before {
  transform: scaleY(1);
}

.fare-option:hover {
  border-color: #ff6b35;
  background: #fff8f6;
  transform: translateX(4px);
}

.fare-option.flight-selected {
  border-color: #28a745;
  background: #f8fff9;
  position: relative;
  box-shadow: 0 2px 8px rgba(40, 167, 69, 0.2);
}

.fare-option.fare-selected {
  border-color: #ff6b35;
  background: #fff8f6;
  position: relative;
  box-shadow: 0 2px 8px rgba(255, 107, 53, 0.2);
  transform: translateX(4px);
}

.fare-option.fare-selected::before {
  transform: scaleY(1);
}

