.fare-radio {
  flex-shrink: 0;
}

.fare-radio input[type="radio"] {
  width: 18px;
  height: 18px;
  accent-color: #ff6b35;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.fare-radio input[type="radio"]:checked {
  transform: scale(1.1);
}

.fare-details {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.fare-type {
  background: #fff3cd;
  color: #856404;
  font-size: 11px;
  font-weight: 600;
  padding: 3px 8px;
  border-radius: 12px;
  border: 1px solid #ffeaa7;
  display: inline-block;
  width: fit-content;
}

.fare-services {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.fare-services span {
  background: #e9ecef;
  color: #495057;
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 8px;
  border: 1px solid #dee2e6;
  transition: all 0.2s ease;
}

.fare-services span:hover {
  background: #ff6b35;
  color: white;
  border-color: #ff6b35;
}

.more-fares-btn {
  background: #6c757d;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 8px 16px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  transition: all 0.2s ease;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.more-fares-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.more-fares-btn:hover::before {
  left: 100%;
}

.more-fares-btn:hover {
  background: #5a6268;
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(108, 117, 125, 0.3);
}

.dropdown-arrow {
  font-size: 10px;
  transition: transform 0.2s ease;
}

.more-fares-btn:hover .dropdown-arrow {
  transform: translateY(2px);
}

.book-button {
  background: #ff6b35;
  color: white;
  border: none;
  border-radius: 12px;
  padding: 16px 24px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 8px rgba(255, 107, 53, 0.3);
  position: relative;
  overflow: hidden;
}

.book-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.book-button:hover::before {
  left: 100%;
}

.book-button:hover:not(:disabled) {
  background: #e55a2b;
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(255, 107, 53, 0.4);
}

.book-button:disabled {
  background: #e9ecef;
  color: #6c757d;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.book-button:disabled::before {
  display: none;
}

/* Responsive Design for Flight Cards */
