  .airline-logo {
    width: 48px;
    height: 48px;
  }

  .airline-name {
    font-size: 18px;
  }

  .airport-code {
    font-size: 20px;
  }

  .time {
    font-size: 20px;
  }

  .book-button {
    padding: 18px 28px;
    font-size: 18px;
  }
}

/* Medium-large screens (17-21 inch) */
@media (min-width: 1600px) and (max-width: 1920px) {
