  }

  .book-button {
    padding: 16px 24px;
    font-size: 17px;
  }
}

/* Medium screens (15-16 inch) */
@media (min-width: 1366px) and (max-width: 1599px) {
  .flight-card {
    padding: 22px;
    gap: 22px;
  }

  .flight-card-left {
    flex: 0 0 90px;
  }

  .flight-card-right {
    flex: 0 0 190px;
  }

  .airline-logo {
    width: 40px;
    height: 40px;
  }

  .airline-name {
