    /* padding: 20px; */
    gap: 10px;
  }

  .flight-card-left {
    flex: 0 0 80px;
  }

  .flight-card-right {
    flex: 0 0 160px;
  }

  .airline-logo {
    width: 38px;
    height: 38px;
  }

  .airline-name {
    font-size: 10px;
  }

  .airport-code {
    font-size: 10px;
  }

  .time {
    font-size: 10px;
  }

  .stop-label {
    font-size: 8px;
  }

  .book-button {
    padding: 14px 20px;
    font-size: 15px;
  }
}

/* Small screens (11-12 inch) */
@media (min-width: 768px) and (max-width: 1023px) {
  .flight-card {
    flex-direction: column;
    gap: 18px;
    padding: 18px;
  }

  .flight-card-left {
    flex: none;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 14px;
  }

  .flight-card-center {
    padding: 0;
    order: -1;
  }

  .flight-card-right {
    flex: none;
  }

  .airline-logo {
    width: 36px;
    height: 36px;
  }

  .airline-name {
    font-size: 14px;
  }

  .airport-code {
    font-size: 16px;
  }

  .time {
    font-size: 16px;
  }

  .book-button {
    padding: 13px 18px;
    font-size: 14px;
  }
}

/* Mobile screens */
@media (max-width: 767px) {
  .flight-card {
    padding: 16px;
    margin: 16px 0;
    flex-direction: column;
