    width: 32px;
    height: 32px;
  }

  .airline-name {
    font-size: 13px;
  }

  .flight-number {
    font-size: 11px;
  }

  .airport-code {
    font-size: 14px;
  }

  .time {
    font-size: 14px;
  }

  .date {
    font-size: 10px;
  }

  .fare-price {
    font-size: 13px;
  }

  .book-button {
    padding: 12px 16px;
    font-size: 13px;
  }
}

/* Layover Information Between Flight Segments Styles */
.layover-info-between {
  margin: 16px 0;
  padding: 12px 16px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
  font-size: 13px;
  color: #666;
  display: flex;
  align-items: center;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
}

.layover-info-between::before {
  content: '';
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #e9ecef;
}

.layover-info-between::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #e9ecef;
}

.layover-location {
  font-weight: 600;
  color: #2c3e50;
}

.layover-separator {
  color: #ccc;
  font-weight: 300;
}

/* .layover-time {
  color: #7f8c8d;
} */

.plane-change-text {
  color: #ff6b35;
  font-weight: 500;
  font-style: italic;
}

/* Flight Details Modal Styles */
.flight-details-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: 20px;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.flight-details-content {
  background: white;
  border-radius: 16px;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.flight-details-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 24px;
  border-radius: 16px 16px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.flight-route-info {
  flex: 1;
}

.route-display {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 8px;
}

.flight-date {
  font-size: 16px;
  opacity: 0.9;
}

.selected-fare-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
}

.fare-type-badge {
  background: rgba(255, 255, 255, 0.2);
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  backdrop-filter: blur(10px);
}


.close-details-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
}

.close-details-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.flight-details-tabs {
  display: flex;
  background: #f8f9fa;
  border-bottom: 1px solid #e9ecef;
  /* overflow-x: auto; */
}

.tab-button {
  background: none;
  border: none;
  padding: 16px 24px;
  font-size: 14px;
  font-weight: 600;
  color: #6c757d;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
  border-bottom: 3px solid transparent;
}

.tab-button:hover {
  color: #495057;
  background: #e9ecef;
}

.tab-button.active {
  color: #ff6b35;
  border-bottom-color: #ff6b35;
  background: white;
}

.tab-content {
  padding: 24px;
}

.loading-spinner {
  text-align: center;
  padding: 40px;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #ff6b35;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 16px;
}

/* Flight Details Tab */
.flight-details-tab {
  max-width: 100%;
}

.flight-segments {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.flight-segment {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
  border: 1px solid #e9ecef;
}

.segment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid #dee2e6;
}

.segment-airline-logo {
  width: 40px;
  height: 40px;
  object-fit: contain;
  border-radius: 8px;
}

.segment-class {
  background: #ff6b35;
  color: white;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
}

.segment-route {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.departure-details,
.arrival-details {
  text-align: center;
  flex: 1;
}

.departure-details .time,
.arrival-details .time {
  font-size: 24px;
