.sort-left {
  display: flex;
  align-items: center;
  gap: 16px;
}

.sort-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

/* Filters Toggle Button */
.filters-toggle-btn {
  padding: 8px 16px;
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  color: #666;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.filters-toggle-btn:hover {
  background: #e9ecef;
  color: #333;
}

.filters-toggle-btn.active {
