/* Responsive Design for Filters */
@media (max-width: 1024px) {
  .main-content-container {
    flex-direction: column;
    gap: 16px;
  }

  .filters-sidebar {
    width: 100%;
    position: static;
    order: -1;
    max-height: 60vh;
    overflow-y: auto;
  }

  .filters-sidebar:not(:empty) {
    margin-bottom: 16px;
  }
}

@media (max-width: 768px) {

  .sort-left,
  .sort-right {
    flex-direction: column;
    gap: 12px;
    align-items: stretch;
  }

  .sort-buttons {
    justify-content: center;
  }

  .filters-sidebar {
    padding: 16px;
    max-height: 50vh;
    overflow-y: auto;
  }

  .filter-section {
    margin-bottom: 20px;
  }
}

/* Loading State Container Styles */
.loading-state-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  z-index: 9999;
  overflow: hidden;
}

/* Search details bar in loading state */
.loading-state-container .search-details-bar {
  width: 100%;
  flex-shrink: 0;
  background: #2c3e50 !important;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15) !important;
  position: static !important;
  z-index: 1;
  margin-bottom: 0 !important;
  top: auto !important;
  left: auto !important;
  right: auto !important;
}

.loading-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 100%;
  padding: 20px;
  gap: 30px;
}

.loading-gif {
  width: 90%;
  max-width: 90%;
  height: auto;
  max-height: calc(100vh - 300px);
  /* object-fit: contain; */
  display: block;
  margin: 0 auto;
  border-radius: 8px;
}

.loading-message {
  font-size: 26px;
  font-weight: 600;
  color: #4a5568;
  text-align: center;
  margin: 0;
  padding: 0 20px;
  animation: pulse 2s ease-in-out infinite;
  white-space: nowrap;
}

/* Responsive breakpoints for different laptop sizes */

/* Large Laptops (1440px and above) */
@media (min-width: 1440px) {
  .loading-gif {
    max-width: 100%;
    max-height: calc(100vh - 320px);
  }

  .loading-message {
    font-size: 28px;
  }

  .loading-content {
    gap: 40px;
  }
}

/* Standard Laptops (1024px - 1439px) */
@media (min-width: 1024px) and (max-width: 1439px) {
  .loading-gif {
    max-width: 100%;
    max-height: calc(100vh - 280px);
  }

  .loading-message {
    font-size: 24px;
  }

  .loading-content {
    gap: 28px;
    padding: 15px;
  }
}

/* Small Laptops (768px - 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
  .loading-gif {
    max-width: 100%;
    max-height: calc(100vh - 260px);
    width: 100%;
  }

  .loading-message {
    font-size: 22px;
  }

  .loading-content {
    gap: 24px;
    padding: 15px;
  }
}

/* Tablets and Small Screens (481px - 767px) */
@media (max-width: 767px) {
  .loading-gif {
    width: 100%;
    max-height: calc(100vh - 280px);
  }

  .loading-message {
    font-size: 20px;
    white-space: normal;
    line-height: 1.4;
  }

  .loading-content {
    gap: 20px;
    padding: 10px;
  }
}

/* Mobile Devices (up to 480px) */
@media (max-width: 480px) {
  .loading-gif {
    width: 100%;
    max-height: calc(100vh - 260px);
  }

  .loading-message {
    font-size: 18px;
    white-space: normal;
    line-height: 1.4;
  }

  .loading-content {
    gap: 16px;
    padding: 10px;
  }

  .loading-state-container .search-details-bar {
    font-size: 14px;
  }
}

/* Special adjustments for very small laptop heights */
@media (max-height: 700px) {
  .loading-gif {
    max-height: calc(100vh - 240px);
  }

  .loading-message {
    font-size: 20px;
  }

  .loading-content {
    gap: 16px;
    padding: 10px;
  }
}

/* Special adjustments for very tall screens */
@media (min-height: 1080px) {
  .loading-gif {
    max-height: calc(100vh - 350px);
  }

  .loading-content {
    gap: 50px;
  }
}

/* Keep the old styles for backward compatibility if needed */
.loading-video-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.6;
}

@keyframes pulse {

  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: 0.7;
    transform: scale(1.05);
  }
}