* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.ct-header {
  width: 100%;
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px) saturate(180%);
  border-bottom: 1px solid #ececec;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.10);
  transition: background 0.3s, box-shadow 0.3s;
}

.ct-navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 48px 0 48px;
  background: transparent;
}

.ct-logo-section {
  display: flex;
  align-items: center;
  gap: 14px;
}

.ct-logo-image {
  width: 170px;
  height: 34px;
  filter: drop-shadow(0 2px 8px rgba(60, 72, 88, 0.10));
}

.ct-logo-text {
  font-size: 1.7rem;
  font-weight: 700;
  color: #ff5a1f;
  letter-spacing: -1px;
  margin-left: 6px;
}

.ct-logo-sep {
  color: #bdbdbd;
  margin: 0 10px;
  font-size: 1.2rem;
}

.ct-logo-flipkart {
  color: #757575;
  font-size: 1.13rem;
  letter-spacing: 0.5px;
}

.ct-logo-flipkart-italic {
  font-style: italic;
  font-weight: 600;
}

.ct-navbar-right {
  display: flex;
  align-items: center;
  gap: 32px;
  text-decoration: none;
}

.ct-navbar-link {
  display: flex;
  align-items: center;
  font-size: 1.09rem;
  color: #23272f;
  cursor: pointer;
  transition: color 0.2s, transform 0.2s;
  gap: 7px;
  font-weight: 500;
}

.ct-navbar-link:hover {
  color: #ff5a1f;
  transform: translateY(-2px) scale(1.04);
}

.ct-navbar-icon {
  font-size: 1.22rem;
  margin-right: 2px;
  color: #5f99e1;
  opacity: 0.85;
}

.ct-login-btn {
  margin-left: 22px;
  padding: 8px 32px;
  border: none;
  background: linear-gradient(90deg, #5f99e1 0%, #ff8c42 100%);
  color: #fff;
  font-weight: 700;
  border-radius: 12px;
  font-size: 1.09rem;
  cursor: pointer;
  box-shadow: 0 2px 8px 0 rgba(255, 90, 31, 0.10);
  transition: background 0.2s, color 0.2s, transform 0.2s, box-shadow 0.2s;
  letter-spacing: 0.5px;
}

.ct-login-btn:hover {
  background: linear-gradient(90deg, #ff8c42 0%, #5f99e1 100%);
  color: #fff;
  transform: scale(1.06);
  box-shadow: 0 4px 16px 0 rgba(255, 90, 31, 0.15);
}

.ct-nav-tabs {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 28px;
  padding: 18px 0 5px 0;
  background: transparent;
  flex-wrap: wrap;
}

.ct-nav-tab {
  display: flex;
  align-items: center;
  gap: 11px;
  font-size: clamp(0.85rem, 1.8vw, 1rem);
  color: #23272f;
  background: none;
  border: none;
  border-radius: 12px;
  padding: 6px 16px;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.2s, color 0.2s, transform 0.2s, box-shadow 0.2s;
  box-shadow: none;
  text-decoration: none;
}

.ct-nav-tab:visited,
.ct-nav-tab:active,
.ct-nav-tab:hover {
  text-decoration: none;
}

.ct-nav-tab:hover {
  background: rgba(255, 90, 31, 0.07);
  color: #ff5a1f;
  transform: translateY(-2px) scale(1.04);
  box-shadow: 0 2px 8px 0 rgba(255, 90, 31, 0.08);
}

.ct-nav-tab-active {
  font-weight: 700;
  transform: scale(1.03);
}
.ct-tab-icon-text{
  font-weight: 700;
  color: #1f53ff;
  transition: filter 0.2s ease;
  filter: grayscale(100%);
}
