.ct-logout-btn.professional {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  padding: 0;
  margin: 0 auto;
  border: none;
  border-radius: 50%;
  background: #f8f9fb;
  color: #5f99e1;
  font-size: 18px;
  box-shadow: 0 1px 4px rgba(60, 72, 88, 0.07);
  cursor: pointer;
  transition: background 0.18s, color 0.18s, box-shadow 0.18s, transform 0.13s;
}

.ct-logout-btn.professional:hover {
  background: #ffede3;
