}

.ct-user-avatar {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #ffede3;
  color: #5f99e1;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  box-shadow: 0 1px 4px rgba(60, 72, 88, 0.07);
  user-select: none;
  margin-right: 4px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

.ct-user-container {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 6px 18px 6px 10px;
  border-radius: 20px;
  background: #5f99e1;
  border: 1.5px solid #5f99e1;
  box-shadow: 0 1px 4px rgba(255, 90, 31, 0.04);
  cursor: pointer;
  transition: box-shadow 0.2s, background 0.2s, border 0.2s, transform 0.15s;
  min-width: 0;
  max-width: 220px;
  overflow: hidden;
}

.ct-user-container:hover {
  box-shadow: 0 4px 16px rgba(255, 90, 31, 0.10);
  background: #ffede3;
  border: 1.5px solid #5f99e1;
  transform: translateY(-2px) scale(1.03);
}

.ct-user-avatar:hover {
  background: #5f99e1;
  color: #fff;
}

.ct-user-container span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.ct-user-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  left: auto;
  margin-top: 6px;
  min-width: 180px;
  background: #fff;
  box-shadow: 0 4px 16px rgba(60, 72, 88, 0.13);
  border-radius: 10px;
  z-index: 1000;
  padding: 6px 0;
  border: 1.5px solid #ffe0cc;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}


/* Responsive Design - Desktop Only Elements */

.desktop-only {
  display: flex;
}

.mobile-only {
  display: none;
}


/* Mobile Menu Styles */

.mobile-menu-toggle {
  display: none;
}

.mobile-menu-btn {
  background: none;
  border: none;
  font-size: 24px;
  color: #5f99e1;
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
  transition: background 0.2s, color 0.2s;
}

