  }
}

@keyframes slideInRight {
  from {
      transform: translateX(100%);
  }
  to {
      transform: translateX(0);
  }
}


/* Responsive Breakpoints */

/* Responsive Design */
/* 13-inch and 14-inch screens (1280px - 1440px) */
@media (min-width: 1280px) and (max-width: 1440px) {
  .ct-navbar {
    padding: 18px 40px 0 40px;
  }

  .ct-logo-image {
    width: 150px;
    height: 30px;
  }

  .ct-logo-text {
    font-size: 1.5rem;
  }

  .ct-logo-sep {
    font-size: 1.1rem;
    margin: 0 8px;
  }

  .ct-logo-flipkart {
    font-size: 1rem;
  }

  .ct-navbar-right {
    gap: 28px;
  }

  .ct-navbar-link {
    font-size: 1rem;
    gap: 6px;
  }

  .ct-navbar-icon {
    font-size: 1.1rem;
  }

  .ct-login-btn {
    margin-left: 18px;
    padding: 10px 20px;
    font-size: 0.95rem;
  }

  .ct-user-menu {
    right: 40px;
  }

  .ct-user-dropdown {
    min-width: 200px;
  }
}

@media (max-width: 1024px) {
  .ct-navbar {
      padding: 20px 24px 0 24px;
  }
  .ct-nav-tabs {
      gap: 16px;
      padding: 20px 0 5px 0;
  }
  .ct-nav-tab {
      padding: 8px 20px;
      font-size: 0.98rem;
  }
  .ct-tab-icon {
      font-size: 1.2rem;
  }
}

@media (max-width: 768px) {
  .desktop-only {
      display: none;
  }
  .mobile-only {
      display: block;
  }
  .mobile-menu-toggle {
      display: block;
  }
  .ct-navbar {
      padding: 16px 20px 0 20px;
  }
  .ct-logo-image {
      width: 140px;
      height: 28px;
  }
  .ct-nav-tabs {
      gap: 12px;
      padding: 16px 10px 5px 10px;
      overflow-x: auto;
      justify-content: flex-start;
      -webkit-overflow-scrolling: touch;
  }
  .ct-nav-tab {
      padding: 8px 16px;
      font-size: 0.95rem;
      min-width: fit-content;
      white-space: nowrap;
  }
  .ct-tab-icon {
      font-size: 1.1rem;
  }
  .ct-nav-tab span {
      font-size: 0.85rem;
  }
}

@media (max-width: 480px) {
  .ct-navbar {
      padding: 12px 16px 0 16px;
  }
  .ct-logo-image {
      width: 120px;
      height: 24px;
  }
  .ct-nav-tabs {
      gap: 8px;
      padding: 12px 8px 5px 8px;
  }
  .ct-nav-tab {
      padding: 6px 12px;
      font-size: 0.85rem;
  }
  .ct-tab-icon {
      font-size: 1rem;
  }
  .ct-nav-tab span {
      font-size: 0.78rem;
  }
  .mobile-menu-content {
      width: 280px;
  }
  .mobile-menu-header {
      padding: 16px;
  }
  .mobile-logo {
      width: 120px;
      height: 24px;
  }
  .mobile-navbar-link {
      padding: 14px 16px;
      font-size: 15px;
  }
  .mobile-user-section {
      padding: 16px;
  }
}


/* Touch-friendly interactions for mobile */

@media (hover: none) and (pointer: coarse) {
  .ct-nav-tab:hover {
      transform: none;
  }
  .ct-navbar-link:hover {
      transform: none;
  }
  .ct-login-btn:hover {
      transform: none;
  }
  .ct-user-container:hover {
      transform: none;
  }
  .ct-logout-btn.professional:hover {
      transform: none;
  }
  .mobile-login-btn:hover {
      transform: none;
  }
}


/* High contrast mode support */

@media (prefers-contrast: high) {
  .ct-header {
      border-bottom: 2px solid #000;
  }
  .ct-navbar-divider {
      background-color: #000;
      opacity: 1;
  }
  .mobile-menu-overlay {
      background: rgba(0, 0, 0, 0.8);
  }
}


/* Reduced motion support */

@media (prefers-reduced-motion: reduce) {
  .ct-header,
  .ct-navbar-link,
  .ct-login-btn,
  .ct-nav-tab,
  .ct-tab-icon,
  .ct-user-container,
  .ct-user-avatar,
  .ct-logout-btn.professional,
  .mobile-menu-btn,
  .mobile-close-btn,
  .mobile-navbar-link,
  .mobile-logout-btn,
  .mobile-login-btn {
      transition: none;
  }
  .mobile-menu-overlay,
  .mobile-menu-content {
      animation: none;
  }
}