}

.business-form-label {
  display: block;
  font-weight: 600;
  color: #495057;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.business-form-textarea {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 0.95rem;
  font-family: inherit;
  resize: vertical;
  transition: all 0.3s ease;
  background: #ffffff;
}

.business-form-textarea:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
  background: #ffffff;
}

.business-form-textarea::placeholder {
  color: #6c757d;
  font-style: italic;
}

.business-error-text {
  display: block;
  color: #dc3545;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  font-weight: 500;
}

.business-submit-section {
  margin-top: 1.5rem;
  text-align: center;
}

.business-submit-btn {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
  border: none;
  color: #ffffff;
  font-weight: 600;
  padding: 0.875rem 2rem;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(40, 167, 69, 0.3);
}

.business-submit-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #218838 0%, #1ea085 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(40, 167, 69, 0.4);
}

.business-submit-btn:disabled {
  background: #6c757d;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.business-submit-btn.processing {
  background: #6c757d;
  cursor: not-allowed;
}

/* Responsive Design for Payment Type */
@media (max-width: 768px) {
  .payment-type-toggle {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .payment-type-option {
    padding: 1rem;
  }
  
  .payment-type-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  .payment-type-label {
    font-size: 0.9rem;
  }
}