.contact-page-modern {
  /* min-height: 100vh; */
  /* background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%); */
  margin-top: 9%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 10px 60px 10px;
}

.contact-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 8px 32px rgba(60, 60, 120, 0.12), 0 1.5px 6px rgba(60, 60, 120, 0.08);
  padding: 2.5rem 2rem 2rem 2rem;
  max-width: 480px;
  width: 100%;
  margin-bottom: 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-title {
  font-size: 2.2rem;
  font-weight: 700;
  color: #1a237e;
  margin-bottom: 0.5rem;
  letter-spacing: 0.5px;
}

.contact-desc {
  color: #374151;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

.contact-info-group {
  width: 100%;
  margin: 1.5rem 0 1.5rem 0;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.contact-info-item {
  font-size: 1.15rem;
  color: #222;
  background: #f3f6fa;
  border-radius: 8px;
  padding: 0.9rem 1.2rem;
  box-shadow: 0 1px 4px rgba(60, 60, 120, 0.06);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: background 0.2s;
}

.contact-info-item a {
  color: #1976d2;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}

.contact-info-item a:hover {
  color: #0d47a1;
  text-decoration: underline;
}

.contact-support {
  color: #607d8b;
  font-size: 1rem;
  margin-top: 0.5rem;
  text-align: center;
}

@media (max-width: 600px) {
  .contact-card {
    padding: 1.2rem 0.7rem 1.2rem 0.7rem;
    max-width: 98vw;
  }
  .contact-title {
    font-size: 1.5rem;
  }
  .contact-info-item {
    font-size: 1rem;
    padding: 0.7rem 0.7rem;
  }
} 