/* Bus Booking Container */
.bus-booking-container {
    position: relative;
    min-height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    overflow: hidden;
  }
  
  .bus-booking-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 1;
  }
  
  .bus-booking-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(2px);
    z-index: 2;
  }
  
  .bus-booking-content {
    position: relative;
    z-index: 3;
    width: 100%;
    max-width: 1200px;
    display: flex;
    justify-content: center;
  }
  
  /* Bus Booking Card */
  .bus-booking-card {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    width: 100%;
    max-width: 900px;
  }
  
  .bus-booking-title {
    font-size: 18px;
    font-weight: 500;
    color: #666;
    margin-bottom: 30px;
    text-align: center;
    letter-spacing: 0.5px;
  }
  
  /* Bus Booking Form */
  .bus-booking-form {
    width: 100%;
  }
  
  .bus-booking-form-row {
    display: grid;
    grid-template-columns: 1fr auto 1fr 1fr;
    gap: 20px;
    align-items: end;
    margin-bottom: 30px;
  }
  
  /* Input Groups */
  .bus-booking-input-group {
    position: relative;
    display: flex;
    flex-direction: column;
  }
  
  .bus-booking-field-label {
    font-size: 14px;
    font-weight: 500;
    color: #666;
    margin-bottom: 8px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }
  
  .bus-booking-input-container {
    position: relative;
  }
  
  .bus-booking-input {
    width: 100%;
    padding: 16px 20px;
    border: 2px solid #e1e5e9;
    border-radius: 12px;
    font-size: 16px;
    font-weight: 600;
    color: #333;
    background: #fff;
    transition: all 0.3s ease;
    outline: none;
  }
  
  .bus-booking-input:focus {
    border-color: #4f46e5;
    box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
  }
  
  .bus-booking-input::placeholder {
    color: #999;
    font-weight: 400;
  }
  
  /* Date Container */
  .bus-booking-date-container {
    position: relative;
    display: flex;
    align-items: center;
    padding: 16px 20px;
    border: 2px solid #e1e5e9;
    border-radius: 12px;
    background: #fff;
    transition: all 0.3s ease;
    cursor: pointer;
  }
  
  .bus-booking-date-container:hover {
    border-color: #4f46e5;
  }
  
  .bus-booking-date-container:focus-within {
    border-color: #4f46e5;
    box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
  }
  
  .bus-booking-date-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    z-index: 10;
  }
  
  /* Alternative approach for better browser compatibility */
  .bus-booking-date-input::-webkit-calendar-picker-indicator {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
  }
  
  /* Firefox date input styling */
  .bus-booking-date-input::-moz-calendar-picker-indicator {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
  }
  
  /* Ensure the date input is properly positioned */
  .bus-booking-date-input:focus {
    outline: none;
  }
  
  /* Make sure the container shows focus state when date input is focused */
  .bus-booking-date-container:has(.bus-booking-date-input:focus) {
    border-color: #4f46e5;
    box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
  }
  
  .bus-booking-date-display {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1;
  }
  
  .bus-booking-date-day {
    font-size: 24px;
    font-weight: 700;
    color: #333;
    line-height: 1;
  }
  
  .bus-booking-date-month {
    font-size: 14px;
    font-weight: 500;
    color: #333;
    margin-top: 2px;
  }
  
  .bus-booking-date-weekday {
    font-size: 12px;
    color: #666;
    margin-top: 2px;
  }
  
  .bus-booking-date-placeholder {
    font-size: 16px;
    color: #999;
    font-weight: 400;
  }
  
  .bus-booking-date-icon {
    color: #4f46e5;
    margin-left: 10px;
  }
  
  /* Swap Button */
  .bus-booking-swap-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background: #f8f9fa;
    border: 2px solid #e1e5e9;
    border-radius: 50%;
    color: #666;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 8px;
  }
  
  .bus-booking-swap-btn:hover {
    background: #4f46e5;
    color: #fff;
    border-color: #4f46e5;
    transform: rotate(180deg);
  }
  
  /* Search Button */
  .bus-booking-search-btn {
    width: 100%;
    padding: 18px 30px;
    background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%);
    color: #fff;
    border: none;
    border-radius: 12px;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(79, 70, 229, 0.3);
  }
  
  .bus-booking-search-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(79, 70, 229, 0.4);
  }
  
  .bus-booking-search-btn:active {
    transform: translateY(0);
  }
  
  /* Suggestions Dropdown */
  .bus-booking-suggestions {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #fff;
    border: 1px solid #e1e5e9;
    border-radius: 12px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    max-height: 200px;
    overflow-y: auto;
    margin-top: 4px;
  }
  
  .bus-booking-suggestion-item {
    padding: 12px 20px;
    cursor: pointer;
    transition: background-color 0.2s ease;
    border-bottom: 1px solid #f1f3f4;
  }
  
  .bus-booking-suggestion-item:last-child {
    border-bottom: none;
  }
  
  .bus-booking-suggestion-item:hover {
    background-color: #f8f9fa;
  }
  
  .bus-booking-city-name {
    font-size: 14px;
    font-weight: 600;
    color: #333;
    margin-bottom: 2px;
  }
  
  .bus-booking-city-country {
    font-size: 12px;
    color: #666;
  }
  
  /* Error Messages */
  .bus-booking-error-msg-alert {
    position: absolute;
    top: 100%;
    left: 0;
    font-size: 12px;
    color: #ef4444;
    margin-top: 4px;
    font-weight: 500;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .bus-booking-container {
      padding: 10px;
    }
    
    .bus-booking-card {
      padding: 30px 20px;
      border-radius: 16px;
    }
    
    .bus-booking-form-row {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    
    .bus-booking-swap-btn {
      order: 2;
      align-self: center;
      margin: 10px 0;
    }
    
    .bus-booking-input-group:nth-child(1) {
      order: 1;
    }
    
    .bus-booking-input-group:nth-child(3) {
      order: 3;
    }
    
    .bus-booking-input-group:nth-child(4) {
      order: 4;
    }
    
    .bus-booking-title {
      font-size: 16px;
      margin-bottom: 25px;
    }
    
    .bus-booking-input {
      padding: 14px 16px;
      font-size: 14px;
    }
    
    .bus-booking-date-container {
      padding: 14px 16px;
    }
    
    .bus-booking-date-day {
      font-size: 20px;
    }
    
    .bus-booking-search-btn {
      padding: 16px 25px;
      font-size: 16px;
    }
  }
  
  @media (max-width: 480px) {
    .bus-booking-card {
      padding: 25px 15px;
    }
    
    .bus-booking-title {
      font-size: 14px;
    }
    
    .bus-booking-field-label {
      font-size: 12px;
    }
    
    .bus-booking-input {
      padding: 12px 14px;
      font-size: 14px;
    }
    
    .bus-booking-date-container {
      padding: 12px 14px;
    }
    
    .bus-booking-date-day {
      font-size: 18px;
    }
    
    .bus-booking-search-btn {
      padding: 14px 20px;
      font-size: 14px;
    }
  }
  
  /* Animation for form appearance */
  @keyframes busBookingSlideInUp {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .bus-booking-card {
    animation: busBookingSlideInUp 0.6s ease-out;
  }
  
  /* Focus states for accessibility */
  .bus-booking-input:focus,
  .bus-booking-date-container:focus-within,
  .bus-booking-swap-btn:focus,
  .bus-booking-search-btn:focus {
    outline: 2px solid #4f46e5;
    outline-offset: 2px;
  }
  
  /* Loading state for search button */
  .bus-booking-search-btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
    transform: none;
  }
  
  .bus-booking-search-btn:disabled:hover {
    transform: none;
    box-shadow: 0 4px 15px rgba(79, 70, 229, 0.3);
  }