.footer {
  background-color: #0e1229;
  color: #d1d1d1;
  padding: 40px 20px 20px 20px;
  font-family: 'Segoe UI', sans-serif;
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.footer-column {
  flex: 1 1 200px;
  min-width: 240px;
  text-align: left;
}

.footer-column h3,
.footer-column h4 {
  color: #ffffff;
  margin-bottom: 15px;
  font-size: 20px;
}

.footer-column p,
.footer-column li,
.footer-column a {
  font-size: 15px;
  color: #d1d1d1;
  line-height: 1.6;
  text-decoration: none;
}

.footer-column ul {
  list-style: none;
  padding: 0;
}

.footer-column li {
  margin-bottom: 10px;
}

.footer-column a:hover {
  color: #4a90e2;
}

.brand-icon {
  margin-right: 10px;
  font-size: 22px;
  vertical-align: middle;
  color: #4a90e2;
}

.social-icons {
  margin-top: 15px;
}

.social-icons a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 14px;
  font-size: 22px;
  color: #d1d1d1;
  background: #181c36;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  box-shadow: 0 2px 8px rgba(74, 144, 226, 0.08);
  transition: color 0.3s, background 0.3s, transform 0.2s;
}

.social-icons a:hover {
  color: #fff;
  background: linear-gradient(135deg, #4a90e2 60%, #50e3c2 100%);
  transform: scale(1.13) translateY(-2px);
  box-shadow: 0 4px 16px rgba(74, 144, 226, 0.18);
}

.subscribe-form {
  display: flex;
  margin-top: 12px;
}

.subscribe-form input {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 4px 0 0 4px;
  background-color: #1c203b;
  color: white;
  font-size: 14px;
}

.subscribe-form button {
  padding: 10px 16px;
  border: none;
  background-color: #4a90e2;
  color: white;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  font-size: 14px;
}

.subscribe-form button:hover {
  background-color: #3b7ed0;
}

.footer-bottom {
  text-align: center;
  font-size: 13px;
  color: #999;
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px solid #1e233d;
}

/* Responsive */
/* Responsive Design */
/* 13-inch and 14-inch screens (1280px - 1440px) */
@media (min-width: 1280px) and (max-width: 1440px) {
  .footer {
    padding: 35px 18px 18px 18px;
  }

  .footer-container {
    gap: 35px;
    max-width: 1100px;
  }

  .footer-column {
    flex: 1 1 180px;
    min-width: 220px;
  }

  .footer-column h3,
  .footer-column h4 {
    font-size: 18px;
    margin-bottom: 12px;
  }

  .footer-column p,
  .footer-column li,
  .footer-column a {
    font-size: 14px;
  }

  .footer-column li {
    margin-bottom: 8px;
  }

  .brand-icon {
    font-size: 20px;
  }

  .social-icons {
    margin-top: 12px;
  }

  .social-icons a {
    margin-right: 12px;
    font-size: 20px;
    width: 40px;
    height: 40px;
  }

  .subscribe-form {
    margin-top: 10px;
  }

  .subscribe-form input {
    padding: 9px;
    font-size: 13px;
  }

  .subscribe-form button {
    padding: 9px 16px;
    font-size: 13px;
  }
}

@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    gap: 30px;
    text-align: center;
  }

  .subscribe-form {
    flex-direction: column;
  }

  .subscribe-form input,
  .subscribe-form button {
    width: 100%;
    border-radius: 4px;
    margin-top: 6px;
  }
}