/* Cab Transfer Booking Page Styles */
.cab-transfer-page {
  min-height: 90vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.cab-hero-section {
  position: relative;
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

/* Background video fallback */
.cab-hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.6) 100%);
  z-index: 1;
}

/* Watermark effect */
.cab-hero-section::after {
  content: '21127868';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 100px,
      rgba(255, 255, 255, 0.03) 100px,
      rgba(255, 255, 255, 0.03) 200px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 100px,
      rgba(255, 255, 255, 0.03) 100px,
      rgba(255, 255, 255, 0.03) 200px
    );
  z-index: 1;
  pointer-events: none;
}

.cab-hero-wrapper {
  position: relative;
  z-index: 2;
  max-width: 1200px;
  width: 100%;
  text-align: center;
}

.cab-main-heading {
  font-size: 3.5rem;
  font-weight: 300;
  color: white;
  margin-bottom: 3rem;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
  letter-spacing: 1px;
  line-height: 1.2;
}

/* Cab Search Form */
.cab-booking-form {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 2.5rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
  max-width: 1000px;
  margin: 0 auto;
}

/* Transfer Type Selection */
.cab-transfer-tabs {
  display: flex;
  justify-content: flex-start;
  gap: 0;
  margin-bottom: 2rem;
  background: #f8f9fa;
  border-radius: 8px;
  padding: 4px;
  width: fit-content;
}

.cab-tab-button {
  background: transparent;
  border: none;
  padding: 12px 24px;
  font-size: 0.95rem;
  font-weight: 600;
  color: #64748b;
  cursor: pointer;
  border-radius: 6px;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.cab-tab-button.active {
  background: white;
  color: #ff6b35;
  box-shadow: 0 2px 8px rgba(255, 107, 53, 0.15);
  border: 2px solid #ff6b35;
}

.cab-tab-button:hover:not(.active) {
  color: #334155;
  background: rgba(255, 255, 255, 0.5);
}

/* Search Fields Layout */
.cab-search-fields {
  display: flex;
  align-items: flex-end;
  gap: 1rem;
  flex-wrap: wrap;
}

.cab-input-group {
  position: relative;
  flex: 1;
  min-width: 180px;
}

.cab-field-label {
  display: block;
  font-size: 0.85rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  text-align: left;
}

.cab-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.cab-input-icon {
  position: absolute;
  left: 16px;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
  z-index: 2;
  pointer-events: none;
}

.cab-text-input {
  width: 100%;
  padding: 16px 16px 16px 48px;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  font-size: 1rem;
  background: white;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.cab-text-input:focus {
  outline: none;
  border-color: #ff6b35;
  box-shadow: 0 0 0 3px rgba(255, 107, 53, 0.1);
}

.cab-text-input::placeholder {
  color: #999;
}

/* Traveller Counter */
.cab-traveller-counter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 16px 16px 16px 48px;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  background: white;
  transition: all 0.3s ease;
}

.cab-traveller-counter:hover {
  border-color: #ff6b35;
  background: #fff5f2;
}

.cab-counter-btn {
  background: #f0f0f0;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #666;
  font-size: 18px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.cab-counter-btn:hover:not(:disabled) {
  background: #ff6b35;
  color: white;
  transform: scale(1.1);
}

.cab-counter-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.cab-traveller-count {
  font-size: 1rem;
  font-weight: 600;
  color: #333;
  min-width: 20px;
  text-align: center;
}

/* Search Button */
.cab-search-btn {
  background: linear-gradient(135deg, #ff6b35 0%, #f7931e 100%);
  border: none;
  border-radius: 12px;
  padding: 16px 32px;
  font-size: 1.1rem;
  font-weight: 600;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 15px rgba(255, 107, 53, 0.3);
  min-width: 140px;
  height: fit-content;
  margin-top: 24px;
}

.cab-search-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 107, 53, 0.4);
}

.cab-search-btn:active {
  transform: translateY(0);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .cab-main-heading {
    font-size: 2.5rem;
    margin-bottom: 2rem;
  }

  .cab-booking-form {
    padding: 2rem;
  }

  .cab-search-fields {
    flex-direction: column;
    align-items: stretch;
  }

  .cab-input-group {
    min-width: auto;
    margin-bottom: 1rem;
  }

  .cab-search-btn {
    width: 100%;
    margin-top: 0;
  }
}

@media (max-width: 768px) {
  .cab-hero-section {
    padding: 1rem;
  }

  .cab-main-heading {
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }

  .cab-booking-form {
    padding: 1.5rem;
  }

  .cab-transfer-tabs {
    width: 100%;
    justify-content: center;
  }

  .cab-tab-button {
    flex: 1;
    text-align: center;
  }

  .cab-field-label {
    font-size: 0.8rem;
    margin-bottom: 6px;
  }

  .cab-text-input {
    padding: 14px 14px 14px 44px;
    font-size: 0.95rem;
  }

  .cab-input-icon {
    left: 14px;
  }

  .cab-traveller-counter {
    padding: 14px 14px 14px 44px;
  }
}

@media (max-width: 480px) {
  .cab-main-heading {
    font-size: 1.5rem;
  }

  .cab-booking-form {
    padding: 1rem;
  }

  .cab-tab-button {
    padding: 10px 16px;
    font-size: 0.85rem;
  }

  .cab-field-label {
    font-size: 0.75rem;
    margin-bottom: 4px;
  }

  .cab-text-input {
    padding: 12px 12px 12px 40px;
    font-size: 0.9rem;
  }

  .cab-input-icon {
    left: 12px;
  }

  .cab-traveller-counter {
    padding: 12px 12px 12px 40px;
  }

  .cab-counter-btn {
    width: 28px;
    height: 28px;
    font-size: 16px;
  }

  .cab-search-btn {
    padding: 14px 24px;
    font-size: 1rem;
  }
}
