.hp-page {
  /* background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); */
  min-height: 100vh;
  padding: 20px 0 60px;
}

.hp-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 20px;
  align-items: start;
}

.hp-left {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.hp-right {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.hp-card {
  background: #fff;
  border: 1px solid #d6d7db;
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.hp-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.hp-summary {
  display: flex;
  gap: 16px;
  align-items: flex-start;
  padding-bottom: 16px;
  border-bottom: 1px solid #f2f3f5;
  margin-bottom: 16px;
}

.hp-hotel-img {
  width: 90px;
  height: 90px;
  object-fit: cover;
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.hp-hotel-img:hover {
  transform: scale(1.05);
}

.hp-summary-content {
  flex: 1;
}

.hp-hotel-name {
  font-size: 18px;
  font-weight: 800;
  color: #1f2937;
  margin: 0 0 8px 0;
  background: linear-gradient(135deg, #667eea);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hp-dates {
  font-size: 13px;
  color: #6b7280;
  margin-bottom: 4px;
}

.hp-room-info {
  font-size: 12px;
  color: #9ca3af;
}

.hp-view-details {
  color: #667eea;
  font-size: 13px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 8px 16px;
  background: linear-gradient(135deg, #667eea);
  color: white;
  border-radius: 25px;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.hp-view-details:hover {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.hp-chevron-down {
  transition: transform 0.3s ease;
}

.hp-chevron-up {
  transform: rotate(180deg);
  transition: transform 0.3s ease;
}

.hp-guest-info {
  padding-top: 16px;
}

.hp-guest-row {
  display: flex;
  align-items: center;
  gap: 16px;
}

.hp-guest-item {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.hp-guest-icon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f4f6;
  border-radius: 4px;
  padding: 2px;
}

.hp-guest-icon svg {
  width: 16px;
  height: 16px;
}

.hp-guest-details {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.hp-guest-name {
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}

.hp-guest-primary {
  font-size: 12px;
  color: #2563eb;
  font-weight: 500;
}

.hp-guest-contact {
  font-size: 13px;
  color: #6b7280;
  margin: 0;
}

.hp-guest-divider {
  width: 1px;
  height: 30px;
  background: #e5e7eb;
  margin: 0 8px;
}

/* Detailed Booking Information Styles */
.hp-booking-details {
  margin-top: 16px;
  padding: 16px;
  background: #f8fafc;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
}

.hp-details-section {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.hp-details-icon {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f9ff;
  border: 1px solid #e0f2fe;
  border-radius: 8px;
  margin-right: 12px;
}

.hp-room-icon {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f9ff;
  border: 1px solid #e0f2fe;
  border-radius: 8px;
  margin-right: 12px;
}

.hp-contact-icon {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f9ff;
  border: 1px solid #e0f2fe;
  border-radius: 8px;
  margin-right: 12px;
}

.hp-details-header {
  display: flex;
  align-items: center;
  gap: 12px;
}

.hp-room-section {
  display: flex;
  align-items: center;
  gap: 12px;
}

.hp-contact-section {
  display: flex;
  align-items: center;
  gap: 12px;
}

.hp-room-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.hp-room-name {
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
}

.hp-room-type {
  font-size: 12px;
  color: #6b7280;
}

.hp-contact-text {
  font-size: 13px;
  color: #6b7280;
  font-weight: 500;
}

.hp-dates-row {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-top: 8px;
}

.hp-date-item {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.hp-date-text {
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
}

.hp-date-label {
  font-size: 12px;
  color: #6b7280;
  font-weight: 500;
}

.hp-nights-badge {
  font-size: 12px;
  font-weight: 600;
  color: #2563eb;
  padding: 4px 10px;
  background: #eff6ff;
  border-radius: 6px;
  white-space: nowrap;
}

.hp-date-divider {
  width: 1px;
  height: 28px;
  background: #e5e7eb;
}

.hp-nights-text {
  font-size: 13px;
  font-weight: 600;
  color: #2563eb;
  padding: 4px 8px;
  background: #eff6ff;
  border-radius: 6px;
}

.hp-room-section {
  display: flex;
  align-items: center;
  gap: 12px;
}

.hp-room-icon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f4f6;
  border-radius: 4px;
  padding: 2px;
}

.hp-room-icon svg {
  width: 16px;
  height: 16px;
}

.hp-room-name {
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
}

.hp-room-type {
  font-size: 12px;
  color: #6b7280;
}

.hp-contact-section {
  display: flex;
  align-items: center;
  gap: 12px;
}

.hp-contact-icon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f4f6;
  border-radius: 4px;
  padding: 2px;
}

.hp-contact-icon svg {
  width: 16px;
  height: 16px;
}

.hp-contact-text {
  font-size: 13px;
  color: #6b7280;
}

.hp-discount-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

.hp-section-title {
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 4px;
}

.hp-discount-desc {
  font-size: 12px;
  color: #6b7280;
}

.hp-login-btn {
  background: linear-gradient(135deg, #667eea);
  border: none;
  color: #fff;
  padding: 12px 24px;
  border-radius: 25px;
  font-weight: 700;
  cursor: pointer;
  font-size: 12px;
  text-transform: uppercase;
  white-space: nowrap;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.hp-login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.hp-trip-secure-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
}

.hp-trip-title {
  font-size: 16px;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 4px 0;
}

.hp-trip-subtitle {
  color: #2563eb;
  font-weight: 600;
  font-size: 14px;
  margin-bottom: 8px;
}

.hp-trip-desc {
  font-size: 13px;
  color: #6b7280;
  line-height: 1.5;
}

.hp-trip-link {
  color: #2563eb;
  text-decoration: none;
}

.hp-trip-price {
  font-size: 12px;
  color: #9ca3af;
  margin: 12px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.hp-add-btn {
  background: linear-gradient(135deg, #667eea);
  color: #fff;
  border: none;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(102, 126, 234, 0.3);
}

.hp-add-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.hp-insurance-logos {
  display: flex;
  gap: 12px;
  margin-top: 12px;
}

.hp-logo-text {
  font-size: 10px;
  font-weight: 700;
  color: #2563eb;
  padding: 4px 10px;
  border: 1px solid #bfdbfe;
  border-radius: 4px;
}

.hp-logo-text.hp-health {
  color: #dc2626;
  border-color: #fecaca;
}

.hp-trip-illustration {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 100px;
}

.hp-illustration {
  font-size: 60px;
}

.hp-payment-title {
  font-size: 18px;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 16px 0;
}

.hp-payment-grid {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.hp-payment-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 20px;
  border: 2px solid #d6d7db;
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: #fff;
  position: relative;
  overflow: hidden;
}

.hp-payment-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.1), transparent);
  transition: left 0.5s ease;
}

.hp-payment-item:hover {
  border-color: #667eea;
  background: linear-gradient(135deg, #f8faff, #f0f4ff);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.15);
}

.hp-payment-item:hover::before {
  left: 100%;
}

.hp-payment-item.selected {
  border-color: #667eea;
  background: linear-gradient(135deg, #f0f4ff, #e8f2ff);
  box-shadow: 0 5px 20px rgba(102, 126, 234, 0.2);
}

.hp-payment-left {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.hp-payment-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background: #f8fafc;
  padding: 8px;
}

.hp-payment-icon svg {
  width: 24px;
  height: 24px;
}

.hp-payment-icon.hp-upi {
  background: #fef3c7;
}

.hp-payment-icon.hp-card {
  background: #dbeafe;
}

.hp-payment-icon.hp-emi {
  background: #f0fdf4;
}

.hp-payment-icon.hp-bank {
  background: #e0f2fe;
}

.hp-payment-icon.hp-clock {
  background: #fef3c7;
}

.hp-payment-icon.hp-gift {
  background: #fce7f3;
}

.hp-payment-name {
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 2px;
}

.hp-payment-desc {
  font-size: 12px;
  color: #6b7280;
}

.hp-arrow {
  color: #9ca3af;
  font-size: 14px;
}

.hp-emi-tag {
  background: #22c55e;
  color: #fff;
  font-size: 10px;
  font-weight: 700;
  padding: 4px 10px;
  border-radius: 4px;
  margin-right: 8px;
}

.hp-total-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.hp-total-title {
  font-size: 24px;
  font-weight: 800;
  color: #1f2937;
  margin: 0;
  background: linear-gradient(135deg, #000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hp-total-amount {
  font-size: 26px;
  font-weight: 600;
  color: #667eea;
  margin: 0;
  background: linear-gradient(135deg, #066e58);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hp-breakdown {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-top: 16px;
  border-top: 1px solid #d6d7db;
}

.hp-break-row {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  color: #212a3d;
}

.hp-break-row.hp-discount {
  color: #22c55e;
}

.hp-scan-card {
  text-align: center;
}

.hp-scan-title {
  font-size: 16px;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 4px 0;
}

.hp-scan-subtitle {
  font-size: 12px;
  color: #6b7280;
  margin-bottom: 16px;
}

.hp-qr-placeholder {
  width: 180px;
  height: 180px;
  margin: 0 auto 12px;
  background: #f3f4f6;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.hp-qr-icon {
  font-size: 80px;
  color: #d1d5db;
}

.hp-qr-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  opacity: 1;
  transition: all 0.3s ease;
}

.hp-qr-placeholder:hover .hp-qr-overlay {
  background: rgba(0, 0, 0, 0.5);
}

.hp-qr-btn {
  background: linear-gradient(135deg, #667eea);
  color: #fff;
  border: none;
  padding: 12px 28px;
  border-radius: 25px;
  font-weight: 700;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.hp-qr-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.5);
}

.hp-upi-logos {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-top: 16px;
  flex-wrap: wrap;
}

.hp-upi-logo {
  width: 32px;
  height: 32px;
}

.hp-upi-text {
  font-size: 11px;
  color: #6b7280;
  font-weight: 600;
}

/* QR Modal Styles */
.hp-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.hp-qr-modal {
  background: #fff;
  border-radius: 16px;
  padding: 24px;
  width: 90%;
  max-width: 500px;
  position: relative;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.hp-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.hp-modal-title {
  font-size: 20px;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}

.hp-modal-close {
  background: none;
  border: none;
  font-size: 20px;
  color: #6b7280;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
}

.hp-modal-close:hover {
  background: #f3f4f6;
}

.hp-modal-timer {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #6b7280;
  margin-bottom: 12px;
}

.hp-timer-icon {
  color: #22c55e;
  font-size: 16px;
}

.hp-modal-instruction {
  font-size: 16px;
  color: #374151;
  margin-bottom: 20px;
  font-weight: 500;
}

.hp-modal-upi-logos {
  display: flex;
  gap: 16px;
  margin-bottom: 24px;
  justify-content: center;
}

.hp-modal-upi-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.hp-modal-upi-icon {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
  color: #fff;
}

.hp-modal-upi-icon.gpay {
  background: #4285f4;
}

.hp-modal-upi-icon.paytm {
  background: #00baf2;
}

.hp-modal-upi-icon.phonepe {
  background: #5f259f;
}

.hp-modal-upi-icon.upi {
  background: #1f2937;
}

.hp-modal-upi-item span {
  font-size: 12px;
  color: #6b7280;
  font-weight: 600;
}

.hp-modal-qr-section {
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
}

.hp-modal-qr-code {
  width: 200px;
  height: 200px;
  background: #f9fafb;
  border: 2px solid #d6d7db;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hp-modal-qr-icon {
  font-size: 120px;
  color: #1f2937;
}

.hp-modal-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hp-modal-amount {
  font-size: 18px;
  font-weight: 700;
  color: #1f2937;
}

.hp-modal-cancel {
  background: #fff;
  border: 2px solid #2563eb;
  color: #2563eb;
  padding: 10px 24px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  font-size: 14px;
}

.hp-modal-cancel:hover {
  background: #eff6ff;
}

@media (max-width: 960px) {
  .hp-container {
    grid-template-columns: 1fr;
  }
  
  .hp-right {
    position: static;
  }
  
  .hp-qr-modal {
    width: 95%;
    padding: 20px;
  }
  
  .hp-modal-upi-logos {
    gap: 12px;
  }
  
  .hp-modal-qr-code {
    width: 180px;
    height: 180px;
  }
  
  .hp-modal-qr-icon {
    font-size: 100px;
  }
}

/* Pay Button */
.hp-pay-button {
  width: 100%;
  padding: 18px 24px;
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.hp-pay-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}

.hp-pay-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.hp-pay-note {
  text-align: center;
  margin-top: 12px;
  font-size: 12px;
  color: #6b7280;
}

